<template>
  <div class="checkbox" @click="changeInput">
    <div class="checkbox_box"></div>
    <input type="checkbox" name="check" :id="id" class="checkbox-input"
      @change="changeInput">
    <div class="checkbox_check" :class="isSelected ? 'checked' : ''">
      <i class="ms-icon check wh-20"></i>
    </div>
    <span class="checkbox_label">{{ label }}</span>
  </div>
</template>
<script>
export default {
  name: 'CheckBox',
  props: {
    label: {
      type: String,
      default: '',
    },
    user: {
      type: Boolean,
      default: false,
    },
    id: {
      type: String,
      default: 'check',
    },
  },
  data() {
    return {
      isSelected: false,
    };
  },
  methods: {
    changeInput() {
      this.isSelected = !this.isSelected;
      event.target.checked = this.isSelected;
      this.$emit('input', this.isSelected);
    },
  },
  updated() {
    this.$nextTick(() => {
      if (this.user) {
        this.isSelected = this.user;
      }
    });
  },
};
</script>
<style lang="scss" scoped>
@import "@/assets/variables.scss";

.checkbox{
  position: relative;
  display: flex;
  width: 100%;
  align-items: center;
  margin-bottom: 10px;
  &-input{
    display: none;
  }
  &_box{
    width: 25px;
    height: 25px;
    background: $white;
    border: 2px solid $grey;
    border-radius: 8px;
    cursor: pointer;
  }
  &_check{
    opacity: 0;
    position: absolute;
    left: 0;
    top: 0;
    width: 25px;
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    &.checked{
      opacity: 1;
    }
  }
  &_label{
    cursor: pointer;
    color: $white;
    font-family: $text-font;
    font-size: 17px;
    margin-left: 10px;
  }
}
</style>



