import Vue from 'vue';
import Vuex from 'vuex';
import AuthService from '@/services/auth.service';


Vue.use(Vuex);

const user = JSON.parse(localStorage.getItem('user'));
const initialState = user
  ? { isAuthenticated: true, uuid: user.uid }
  : { isAuthenticated: false, uuid: null };


export default new Vuex.Store({
  state: initialState,
  mutations: {
    login(state, userInfo) {
      state.uuid = userInfo.uid;
      state.isAuthenticated = true;
    },
    logout(state) {
      state.isAuthenticated = false;
    },
    register(state, userInfo) {
      state.uuid = userInfo.uid;
      state.isAuthenticated = false;
    },
    refresh(state) {
      state.isAuthenticated = true;
    },
  },
  actions: {
    async login({ commit }, userInfo) {
      return AuthService.login(userInfo)
        .then((userData) => {
          commit('login', userData);
          return Promise.resolve(userData);
        });
    },
    async logout({ commit }) {
      AuthService.logout();
      commit('logout');
    },
    async refresh({ commit }, userInfo) {
      return AuthService.refresh(userInfo)
        .then(() => commit('refresh'));
    },
    async register({ commit }, userInfo) {
      return AuthService.register(userInfo)
        .then((userData) => {
          commit('register', userData);
          return Promise.resolve(userData);
        });
    },
  },
  getters: {
    isAuthenticated: state => state.isAuthenticated,
    uuid: state => state.uuid,
  },
});



