<template>
  <article class="index page_wrapper">
    <div class="loading" v-if="loading">
      <svg width="32px" height="24px">
        <polyline id="back" points="1 6 4 6 6 11 10 1 12 6 15 6"></polyline>
        <polyline id="front" points="1 6 4 6 6 11 10 1 12 6 15 6"></polyline>
      </svg>
    </div>
    <div class="page_content" v-else>
      <div class="advice">
        <div class="block_title"> </div>
        <div class="advice_wrapper" ref="aWrapper">
          <div class="advice_continue" v-for="a in advice.continue" :key="a.uid">
            <div class="continue_card">
              <img :src="`/i/${a.picture}`" alt="" class="base_image preview_image">
              <div class="continue_actions">
                <div class="continue_action play_button">
                  <a :href="a.type === 'episode'
                    ? `/watch/${a.uid}?c=${a.continue}`
                    : `/${a.uid}?c=${a.continue}`" class="base_link">
                    <i class="ms-icon play wh-20"></i>
                  </a>
                </div>
                <div class="continue_action hide_button">
                  <a class="base_link" @click="hideFromAdvice(a.w_uid)">
                    <i class="ms-icon xmark wh-20"></i>
                  </a>
                </div>
                <div class="continue_runtime">
                  <div class="runtime_wrapper">
                    ~
                     {{ parseInt(parseInt(a.runtime, 10)-(parseInt(a.continue, 10)/60),10) }} 
                  </div>
                </div>
              </div>
            </div>
            <div class="continue_name">{{ a.name }}</div>
            <div class="continue_info">{{ a.additional }}</div>
          </div>
          <div class="advice_new" v-for="a in advice.new" :key="a.uid">
            <div class="new_card">
              <img :src="`/i/${a.picture}`" alt="" class="base_image preview_image">
              <div class="new_actions">
                <div class="new_action play_button">
                  <a :href="a.type === 'movie' ? `/${a.uid}` : `/watch/${a.uid}`" class="base_link">
                    <i class="ms-icon play wh-20"></i>
                  </a>
                </div>
              </div>
            </div>
            <div class="new_name" :title="a.name">{{ a.name }}</div>
            <div class="new_info" :title="a.additional">{{ a.additional }}</div>
          </div>
        </div>
      </div>
      <div class="tvs">
        <div class="block_title"></div>
        <div class="tvs_wrapper">
          <div class="tv_item" v-for="tv in unwatched.tvs" :key="tv.uid">
            <a :href="`/${tv.uid}`" class="base_link">
              <div class="tv_image">
                <img :src="`/i/${tv.poster}`" alt="" class="base_image" v-if="tv.poster">
              </div>
              <div class="tv_name">{{ tv.name }} ({{ tv.original_name }})</div>
            </a>
          </div>
        </div>
      </div>
      <div class="movies">
        <div class="block_title"></div>
        <div class="movies_wrapper">
          <div class="movie_item" v-for="movie in unwatched.movies" :key="movie.uid">
            <a :href="`/${movie.uid}${!movie.seen && movie.continue > 0
              ? `?c=${movie.continue}` : ''}`" class="base_link">
              <div class="movie_image">
                <img :src="`/i/${movie.poster}`" alt="" class="base_image" v-if="movie.poster">
              </div>
              <div class="movie_name">{{ movie.name }} ({{ movie.original_name }})</div>
            </a>
          </div>
        </div>
      </div>
    </div>
  </article>
</template>
<script>
import Exchange from '../services/exchange.service';

export default {
  name: 'Index',
  data() {
    return {
      backend: new Exchange(),
      advice: {
        continue: null,
        new: null,
      },
      unwatched: {
        movies: null,
        tvs: null,
      },
      loading: false,
      movies: null,
      tvs: null,
    };
  },
  methods: {
    retrieveAdvice() {
      this.loading = true;
      this.backend
        .get('/api/user/advice')
        .then((resp) => {
          if (resp) {
            this.advice.continue = resp.body.unwatched;
            this.advice.new = resp.body.new;
            this.unwatched.movies = resp.body.movies;
            this.unwatched.tvs = resp.body.tvs;
          }
        })
        .catch(err => console.error(err))
        .finally(() => {
          this.loading = false;
        });
    },
    hideFromAdvice() {},
  },
  created() {
    this.retrieveAdvice();
  },
};
</script>
<style lang="scss" scoped>
@import "@/assets/variables.scss";

.index{
  .page_content {
    .advice, .movies, .tvs {
      .block_title{
        margin-bottom: 15px;
        font-size: 24px;
        font-family: $title-font;
        color: $white;
        font-weight: bold;
        margin-left: 10px;
      }
    }
    .advice{
      margin-bottom: 20px;
      &_wrapper{
        display: flex;
        overflow-x: auto;
        &::-webkit-scrollbar{
          -webkit-appearance: none;
        }
        .advice{
          &_continue{
            margin-right: 10px;
            &:last-child{
              margin-right: 0;
            }
            .continue{
              &_card{
                position: relative;
                border-radius: 8px;
                &:hover .continue_actions{
                  visibility: visible;
                }
              }
              &_action{
                background: $blue;
                color: $white;
                width: 40px;
                height: 40px;
                font-size: 20px;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 50%;
                margin-right: 10px;
                & > a{
                  height: 100%;
                  width: 100%;
                  display: flex;
                  align-items: center;
                  justify-content: center;
                }
                &:last-child{
                  margin-right: 0;
                }
                &.play_button i{
                  margin-left: 3px;
                }
              }
              &_actions{
                visibility: hidden;
                position: absolute;
                box-sizing: border-box;
                border-radius: 8px;
                width: 100%;
                height: calc(100% - 3px);
                display: flex;
                align-items: center;
                justify-content: center;
                top: 0;
                background: rgba($color: #000000, $alpha: .3);
              }
              &_runtime{
                visibility: visible;
                position: absolute;
                right: 10px;
                bottom: 10px;
                font-family: $text-font;
                color: $white;
                padding: 3px 5px;
                background: rgba($color: #000000, $alpha: .5);
                border-radius: 5px;
              }
              &_name{
                font-family: $title-font;
                color: $white;
                max-width: 280px;
                margin: 0 auto;
                margin-bottom: 5px;
              }
              &_info{
                font-family: $text-font;
                color: $white;
                max-width: 280px;
                overflow-y: hidden;
                text-overflow: ellipsis;
                display: block;
                white-space: nowrap;
                margin: 0 auto;
              }
            }
          }
        }
      }
    }
    .tvs{
      margin-bottom: 20px;
      &_wrapper{
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
        justify-items: center;
        grid-gap: 20px;
        .base_link{
          font-family: $text-font;
          color: $white;
          font-size: 16px;
          text-align: center;
        }
        .tv{
          &_item{
            max-width: 100%;
          }
          &_image{
            margin-bottom: 5px;
            img{
              object-fit: cover;
              aspect-ratio: 2 / 3;
              width: 100%;
              border-radius: 5px;
              pointer-events: none;
            }
          }
          &_name{
            max-width: 190px;
            margin: 0 auto;
          }
        }
      }
    }
    .movies{
      margin-bottom: 20px;
      &_wrapper{
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
        justify-items: center;
        grid-gap: 20px;
        .base_link{
          font-family: $text-font;
          color: $white;
          font-size: 16px;
          text-align: center;
        }
        .movie{
          &_item{
            max-width: 100%;
          }
          &_image{
            margin-bottom: 5px;
            img{
              object-fit: cover;
              aspect-ratio: 2 / 3;
              width: 100%;
              border-radius: 5px;
              pointer-events: none;
            }
          }
          &_name{
            max-width: 190px;
            margin: 0 auto;
          }
        }
      }

    }
  }
}
</style>



