<template>
  <div class="player" id="player" ref="mainPlayer"></div>
</template>
<script>
import Exchange from '../services/exchange.service';

export default {
  name: 'msplayer',
  props: {
    videoSource: {
      type: String,
      default: '',
    },
    title: {
      type: String,
      default: '',
    },
    preview: {
      type: String,
      default: '',
    },
    subtitle: {
      type: String,
      default: '',
    },
    subLang: {
      type: String,
      default: '',
    },
    continue: {
      type: Number,
      default: 0,
    },
    local: {
      type: Boolean,
      default: false,
    },
    filename: {
      type: String,
    },
  },
  data() {
    return {
      backend: new Exchange(),
      playerLangs: {
        eng: '',
        rus: '',
      },
      playerInfo: {
        duration: null,
        time: null,
      },
      player: this.$refs.mainPlayer,
      showPlayer: false,
      Player: null,
      video: null,
    };
  },
  methods: {
    applyListeners() {
      this.$refs.mainPlayer.addEventListener('start', this.playerStarted);
      if ((this.subtitle && this.subLang) || this.local) {
        this.$refs.mainPlayer.addEventListener('fullscreen', this.manageScreen);
        this.$refs.mainPlayer.addEventListener('exitfullscreen', this.manageScreen);
      }
      this.$refs.mainPlayer.addEventListener('buffered', this.playerBuffered);
      this.$refs.mainPlayer.addEventListener('userpause', this.playerPaused);
      window.addEventListener('beforeunload', this.beforeWindowUnload);
      if (navigator.userAgent.match(/iPhone/i)) {
        window.addEventListener('pagehide', this.beforeWindowUnload);
      }
    },
    deactivateListeners() {
      this.$refs.mainPlayer.removeEventListener('start', this.playerStarted);
      if ((this.subtitle && this.subLang) || this.local) {
        this.$refs.mainPlayer.removeEventListener('fullscreen', this.manageScreen);
        this.$refs.mainPlayer.removeEventListener('exitfullscreen', this.manageScreen);
      }
      this.$refs.mainPlayer.removeEventListener('buffered', this.playerBuffered);
      this.$refs.mainPlayer.removeEventListener('userpause', this.playerPaused);
      window.removeEventListener('beforeunload', this.beforeWindowUnload);
      if (navigator.userAgent.match(/iPhone/i)) {
        window.removeEventListener('pagehide', this.beforeWindowUnload);
      }
    },
    encodeUrl(filename, fileType = 'stream') {
      const date = new Date();
      date.setHours(date.getHours() + 3);
      const Y = date.getFullYear();
      const m = date.getMonth() + 1 < 10 ? `0${date.getMonth() + 1}` : date.getMonth() + 1;
      const d = date.getDate() < 10 ? `0${date.getDate()}` : date.getDate();
      const H = date.getHours() < 10 ? `0${date.getHours()}` : date.getHours();
      const D = `${Y}-${m}-${d} ${H}`;
      if (fileType === 'subs') {
        return `${btoa(`${filename}&${JSON.parse(localStorage.getItem('user')).uid}&${D}`)}/${filename}.vtt`;
      }
      return btoa(`${filename}&${JSON.parse(localStorage.getItem('user')).uid}&${D}`);
    },
    playerStarted() {
      this.video = document.querySelector('video');
      this.downloadSubtitles();
      this.Player.api('subtitles', -1);
      if (this.continue && this.continue > 0) {
        this.Player.api('seek', this.continue);
      }
    },
    downloadSubtitles() {
      let subs = '';
      if (this.local) {
        let blob = null;
        this.backend
          .getFiles(`/s/${this.encodeUrl(this.filename, 'subs')}`)
          .then((resp) => {
            if (resp) {
              blob = new Blob([resp], { type: 'text/vtt' });
              subs = `<track default label="${this.playerLangs[this.subLang]}" src="${URL.createObjectURL(blob)}" kind="subtitles" srclang="${this.subLang}" mode="hidden" />`;
            }
          })
          .catch(err => console.error(err))
          .finally(() => {
            this.video.insertAdjacentHTML('beforeend', subs);
          });
      } else if (this.subtitle) {
        this.backend
          .getResp(this.subtitle)
          .then((resp) => {
            if (resp) {
              const blob = new Blob([resp], { type: 'text/vtt' });
              subs = `<track default label="${this.subLang}" src="${URL.createObjectURL(blob)}" kind="subtitles" srclang="${this.subLang}" mode="hidden" />`;
            }
            return { subtitles: subs };
          })
          .catch(err => console.error(err))
          .finally(() => {
            this.video.insertAdjacentHTML('beforeend', subs);
          });
      }
    },
    manageScreen() {
      if (event.type === 'exitfullscreen') {
        this.video.textTracks[0].mode = 'showing';
        this.Player.api('subtitle', -1);
      } else {
        this.video.textTracks[0].mode = 'hidden';
        this.Player.api('subtitle', 0);
      }
      this.playerInfo.time = parseInt(this.Player.api('time'), 10);
    },
    playerBuffered() {
      this.playerInfo.duration = parseInt(this.Player.api('duration'), 10);
      this.playerInfo.time = parseInt(this.Player.api('time'), 10);
    },
    playerPaused() {
      this.playerInfo.time = parseInt(this.Player.api('time'), 10);
    },
    sendPlayerData() {
      this.$emit('unload', this.playerInfo);
    },
    beforeWindowUnload() {
      this.sendPlayerData();
    },
    initPlayer() {
      const filePath = this.local ? `/f/${this.encodeUrl(`${this.filename}.mp4`)}` : this.videoSource;
      const subPath = this.local ? `/s/${this.encodeUrl(this.filename, 'subs')}`
        : `[${this.playerLangs[this.subLang]}]${this.subtitle}`;
      // eslint-disable-next-line
      this.Player = new Playerjs({
        id: 'player',
        file: filePath,
        title: this.title,
        poster: this.preview,
        subtitle: subPath,
      });
      this.showPlayer = true;
      if (this.Player) {
        this.applyListeners();
      }
    },
  },
  created() {
    this.showPlayer = true;
  },
  beforeDestroy() {
    this.deactivateListeners();
  },
};
</script>
<style lang="scss" scoped>
.player{
  width: 100%;
  &.movie_player{
    aspect-ratio: 16 / 10 !important;
  }
  &.tv_player {
    aspect-ratio: 16 / 9;
  }
}
</style>



