import axios from 'axios';
import authHeader from './auth-header';

function getCSRFToken() {
  const cookieValue = document.cookie
    .split('; ')
    .find(cookie => cookie.startsWith('csrftoken='));
  if (cookieValue) {
    return cookieValue.split('=')[1];
  }
  return null;
}

const csftToken = getCSRFToken();

// eslint-disable-next-line
axios.defaults.headers.common['HTTP_X_CSRFTOKEN'] = csftToken;


class Exchange {
  async get(url, parameters = null) {
    this.msg = null;
    return axios
      .get(url, { headers: authHeader(), params: parameters })
      .then((resp) => {
        if (resp.data.status === 'success') {
          return resp.data;
        }
        this.msg = resp.data.message;
        return null;
      })
      .catch(err => console.error(err));
  }

  async getResp(url) {
    delete axios.defaults.headers.common.HTTP_X_CSRFTOKEN;
    this.msg = null;
    return axios
      .get(url)
      .then((resp) => {
        if (resp.data) {
          return resp.data;
        }
        this.msg = `Error while requesting ${url}`;
        return null;
      })
      .catch(err => console.log(err));
  }

  async getFiles(url) {
    this.msg = null;
    return axios
      .get(url, { headers: authHeader() })
      .then(resp => resp.data)
      .catch(err => console.error(err));
  }

  async post(url, data) {
    this.msg = null;
    this.headers = authHeader();
    this.headers['Content-Type'] = data instanceof FormData
      ? 'multipart/form-data'
      : 'application/json';
    return axios
      .post(url, data, { headers: this.headers })
      .then((resp) => {
        if (resp.data.status === 'success') {
          if (resp.data.message) {
            this.msg = resp.data.message;
          }
          return resp.data;
        }
        this.msg = resp.data.message;
        return null;
      })
      .catch(err => console.error(err));
  }

  async delete(url, daTa = {}) {
    this.msg = null;
    this.headers = authHeader();
    this.headers['Content-Type'] = 'application/json';
    return axios
      .delete(url, { data: daTa, headers: this.headers })
      .then((resp) => {
        if (resp.data.status === 'success') {
          return resp.data;
        }
        this.msg = resp.data.message;
        return null;
      })
      .catch(err => console.error(err));
  }

  async beacon(data) {
    this.msg = null;
    this.data = data;
    this.data.headers = authHeader();
    return navigator.sendBeacon('/api/watch/beacon', JSON.stringify(this.data));
  }
}


export default Exchange;



