export default function authHeader(state = 'main') {
  const user = JSON.parse(localStorage.getItem('user'));
  const actions = { main: 'accs_token', refresh: 'rfsh_token' };
  let resp = null;
  if (user) {
    resp = { Authorization: `Bearer ${user[actions[state]]}` };
  }
  return resp;
}



