<template>
  <article class="user page_wrapper">
    <div class="user_profile">
      <div class="block_info">
        <div class="block_title"> </div>
      </div>
      <div class="user_profile-form">
        <div class="leftside" :style="`background: ${userInfo.color}59;`">
          <form class="user_form" @submit.prevent="changeUserInfo">
            <div class="form_row">
              <div class="form_column small">
                <div class="avatar">
                  <file image="avatar" preview name="avatar" draggable class="avatar_image"
                    :existing="userInfo.profile.avatar" v-model="userInfo.profile.newAvatar"
                    :color="userInfo.color" v-if="userInfo.profile.avatar"/>
                </div>
              </div>
              <div class="form_column">
                <input type="text" class="input_wide" required placeholder=""
                  v-model="userInfo.profile.username" id="cLogin">
                <selecter :options="extra.voices" multiple placeholder="  "
                  v-model="userInfo.profile.chosenVoice" updatable ref="userVoiceSelection"/>
                <CheckBox label=" " :user="userInfo.profile.autoSearch"
                  v-model="userInfo.profile.autoSearch" :id="`search_${userInfo.profile.uid}`"/>
                <CheckBox label="   " :user="userInfo.profile.shareDownloads"
                  v-model="userInfo.profile.shareDownloads" :id="`share_${userInfo.profile.uid}`" />
              </div>
            </div>
            <button type="submit" class="btn btn_submit"
              :class="userFormValid ? '': 'not_valid'" :disabled="!userFormValid"
              ref="changeUserInfoButton"></button>
          </form>
        </div>
        <div class="rightside" v-if="userInfo.isAdmin">
          <div class="form_title"> </div>
          <form class="new_user" @submit.prevent="newUserCreate">
            <div class="form_row">
              <input type="text" class="input_wide" placeholder="" id="nuLogin"
                required autocomplete="off" v-model="newUser.username">
              <input type="number" placeholder="" required   class="input_wide"
                v-model="newUser.age" id="nuAge">
            </div>
            <input type="password" placeholder="" required class="input_wide"
              v-model="newUser.password" id="nuPassword">
            <input type="email" placeholder="Email" required class="input_wide"
              v-model="newUser.email" id="nuEmail">
            <button type="submit" class="btn btn_submit"
              ref="newUserButton"></button>
          </form>
        </div>
      </div>
      <button type="button" class="btn btn_submit btn_exit"
        @click="logout">  </button>
    </div>
    <div class="user_history" v-if="userInfo.history && userInfo.history.length > 1">
      <div class="block_info">
        <div class="block_title"> </div>
        <div class="block_action">
          <a href="/user/history" class="base_link" v-show="isHistoryLong"> </a>
        </div>
      </div>
      <div class="user_history-wrapper">
        <div class="history_item" v-for="history in userInfo.history" :key="history.uid">
          <div class="history_item-card">
            <img :src="`/i/${history.picture}`" class="base_image preview_image"
              v-if="history.picture">
            <img src="/static/default-history-image.png" class="base_image preview_image"
              v-else>
            <div class="history_item-actions">
              <div class="delete_button" @click="deleteFromHistory(history.uid)">
                <i class="ms-icon xmark wh-20"></i>
              </div>
            </div>
          </div>
          <div class="history_item-info">
            <div class="info_name">{{ history.name }}</div>
            <div class="info_additional" :title="history.additional">{{ history.additional }}</div>
          </div>
        </div>
      </div>
    </div>
    <div class="user_queue" v-show="queueList && queueList.length >= 1
      || userInfo.queue && userInfo.queue.length >= 1">
      <div class="block_info">
        <div class="block_title"> </div>
        <div class="block_action">
          <a class="base_link" @click="saveQueue" v-if="queueList && queueList.length > 1"
            > </a>
          <a class="base_link clear_all_button" @click="clearQueue"
            v-if="queueList && queueList > 1 || userInfo.queue && userInfo.queue.length > 1">
               </a>
          <a class="base_link download_all_button" @click="downloadAll"
            v-if="queueList && queueList > 1 || userInfo.queue && userInfo.queue.length > 1">
             </a>
        </div>
      </div>
      <div class="user_queue-wrapper">
        <div class="queue_item" v-for="(q, index) in queueList" :key="q[q.type].uid">
          <div class="queue_card">
            <img :src="`/i/${q[q.type].preview}`" class="base_image preview_image">
            <div class="queue_actions">
              <div class="download_button" @click="downloadEl(q[q.type].filename)">
                <i class="ms-icon download wh-20"></i>
              </div>
              <div class="clear_button" @click="clearEl(index, q.type, q[q.type].uid)">
                <i class="ms-icon xmark wh-20"></i>
              </div>
              <div class="runtime"> {{ q[q.type].runtime }}</div>
            </div>
          </div>
          <div class="queue_info">
            <div class="info_data">
              {{ q[q.type].name }} {{ q[q.type].season }}  {{ q[q.type].episode }} 
            </div>
            <div class="info_name" :title="q[q.type].title">{{ q[q.type].title }}</div>
          </div>
        </div>
        <div class="queue_item" v-for="(q, index) in userInfo.queue" :key="q.uid">
          <div class="queue_card">
            <img :src="`/i/${q.picture}`" alt="" class="base_image preview_image">
            <div class="queue_actions">
              <div class="download_button" @click="downloadElFromQ(index, q.uid)">
                <div class="ms-icon download wh-20"></div>
              </div>
              <div class="clear_button" @click="clearElFromQ(index, q.type, q.uid)">
                <i class="ms-icon xmark wh-20"></i>
              </div>
              <div class="runtime"> {{ q.runtime }} </div>
            </div>
          </div>
          <div class="queue_info">
            <div class="info_data">{{ q.name }}</div>
            <div class="info_name" :title="q.additional">{{ q.additional }}</div>
          </div>
        </div>
      </div>
    </div>
    <div class="user_downloads" v-show="userInfo.downloads && userInfo.downloads.length >= 1">
      <div class="block_info">
        <div class="block_title"></div>
        <div class="block_action">
          <a class="base_link clear_all_button" @click="clearDownloads"> </a>
          <a href="/user/downloads" class="base_link"
            v-show="userInfo.downloads && userInfo.downloads.length > 4"> </a>
        </div>
      </div>
      <div class="user_downloads-wrapper">
        <div class="downloads" v-for="(d, index) in userInfo.downloads" :key="d.uid">
          <div class="downloads_card">
            <img :src="`/i/${d.preview}`" class="base_image preview_image">
            <div class="downloads_actions" v-if="d.completed">
              <a :href="`/watch/${d.watch_uid}`" class="base_link watch_button base_button">
                <i class="ms-icon play wh-20"></i>
              </a>
              <div class="clear_button" @click="clearDownload(index, d.uid)">
                <i class="ms-icon xmark wh-20"></i>
              </div>
              <div class="runtime">{{ parseInt(d.runtime / 60, 10) }} </div>
            </div>
            <div class="downloads_progress" v-else>
              <div class="progress_bar">
                <div class="progress_bar-count"
                  :title="convertToText(d.uid)"
                  :style="progressStyles(d.uid)"></div>
              </div>
            </div>
          </div>
          <div class="downloads_info">
            <div class="info_data">{{ d.name }}</div>
            <div class="info_name" v-if="d.title">{{ d.title }}</div>
          </div>
        </div>
      </div>
    </div>
  </article>
</template>
<script>
import Exchange from '../services/exchange.service';
import selecter from '../components/Selecter';
import fileUpload from '../components/FileUploader';
import CheckBox from '../components/Checkbox';

export default {
  name: 'User',
  components: { selecter, CheckBox, file: fileUpload },
  data() {
    return {
      backend: new Exchange(),
      userInfo: {
        history: null,
        downloads: null,
        queue: null,
        profile: {
          uid: null,
          autoSearch: false,
          shareDownloads: false,
          avatar: null,
          chosenVoice: null,
          username: null,
          newAvatar: null,
        },
        isAdmin: false,
        color: null,
      },
      newUser: {
        username: null,
        age: null,
        password: null,
        email: null,
      },
      extra: {},
      isHistoryLong: false,
      queueList: JSON.parse(localStorage.getItem('queue')),
    };
  },
  methods: {
    retrieveUserInfo() {
      this.backend
        .get('/api/user/')
        .then((resp) => {
          if (resp) {
            this.userInfo.history = resp.body.history;
            this.userInfo.downloads = resp.body.downloads;
            this.userInfo.queue = resp.body.queue;
            this.updateUserInfo(resp.extra.user_info);
            this.extra = resp.extra;
            this.isHistoryLong = this.userInfo.history ? this.userInfo.history.length > 4 : false;
          } else {
            console.log(this.backend.msg);
          }
        })
        .catch(err => console.error(err));
    },
    updateUserInfo(userInfo) {
      this.userInfo.profile.uid = userInfo.uid;
      this.userInfo.profile.autoSearch = userInfo.auto_search;
      this.userInfo.profile.shareDownloads = userInfo.share_downloads;
      this.userInfo.profile.chosenVoice = userInfo.chosen_voice;
      this.userInfo.profile.username = userInfo.username;
      this.userInfo.profile.avatar = `/p/${userInfo.avatar}`;
      this.userInfo.isAdmin = userInfo.is_admin;
      this.userInfo.color = userInfo.user_color;
    },
    newUserCreate() {
      const prevText = this.$refs.newUserButton.textContent;
      this.$refs.newUserButton.textContent = '';
      const password = this.newUser.password;
      if (typeof this.newUser.password === 'string') {
        this.newUser.password = [password, password];
      }
      this.backend
        .post('/auth/new/', this.newUser)
        .then((resp) => {
          if (resp) {
            console.log(this.backend.msg);
          }
        })
        .catch(err => console.error(err))
        .finally(() => {
          this.$refs.newUserButton.textContent = prevText;
          this.newUser = {
            login: null, password: null, email: null, age: null,
          };
        });
    },
    changeUserInfo() {
      const data = this.validateFormData();
      const prevText = this.$refs.changeUserInfoButton.textContent;
      this.$refs.changeUserInfoButton.textContent = '';
      const formData = new FormData();
      Object.keys(data)
        .forEach(key => formData.append(key, data[key]));
      formData.append('avatar', this.userInfo.profile.newAvatar);
      formData.delete('newAvatar');
      this.backend
        .post('/api/user/', formData)
        .then((resp) => {
          if (resp) {
            console.log(resp);
          }
        })
        .catch(err => console.error(err))
        .finally(() => {
          this.$refs.changeUserInfoButton.textContent = prevText;
        });
    },
    changeCheckbox(event, data) {
      console.log(event, data);
    },
    validateFormData() {
      const user = JSON.parse(localStorage.getItem('user'));
      const userData = {
        username: this.userInfo.profile.username !== user.username
          ? this.userInfo.profile.username : null,
        auto_search: this.userInfo.profile.autoSearch !== user.auto_search
          ? this.translateFunc(this.userInfo.profile.autoSearch) : null,
        share_downloads: this.userInfo.profile.shareDownloads !== user.shareDownloads
          ? this.translateFunc(this.userInfo.profile.shareDownloads) : null,
        voice: this.userInfo.profile.chosenVoice !== user.chosen_voice
          ? this.userInfo.profile.chosenVoice : null,
      };
      return userData;
    },
    translateFunc(data) {
      return data ? 'on' : 'off';
    },
    logout() {
      this.$store.dispatch('logout');
    },
    clearQueue() {
      if (this.userInfo.queue) {
        this.backend
          .post('/api/user/queue/delete')
          .then((resp) => {
            if (resp) {
              console.log(resp);
              localStorage.removeItem('queue');
            }
          })
          .catch(err => console.error(err))
          .finally(() => {
            setTimeout(() => {
              location.reload();
            }, 1000);
          });
      } else {
        localStorage.removeItem('queue');
      }
    },
    downloadAll() {
      const qId = this.saveQueue(true);
      const postData = { queue_id: qId, type: 'multi' };
      this.backend
        .post('/api/download/', postData)
        .then((resp) => {
          if (resp) {
            console.log(resp.message);
          }
        })
        .catch(err => console.error(err));
    },
    downloadEl(qIndex, fname) {
      this.clearEl(qIndex);
      const postData = { filename: fname };
      this.backend
        .post('/api/download/', postData)
        .then((resp) => {
          console.log(resp, this.backend.msg);
        })
        .catch((err) => {
          console.error(err);
        });
    },
    downloadElFromQ() {
      const postData = { queue_id: this.userInfo.extra.queue_id, type: 'ofq' };
      this.backend
        .post('/api/download/', postData)
        .then((resp) => {
          if (resp) {
            console.log(resp.message);
          }
        })
        .catch(err => console.error(err));
    },
    clearEl(index) {
      const localQueue = JSON.parse(localStorage.getItem('queue'));
      if (localQueue.length > 2) {
        localQueue.splice(index, 1);
        localStorage.setItem('queue', JSON.stringify(localQueue));
      } else {
        localStorage.removeItem('queue');
      }
    },
    clearElFromQ(index, qType, uId) {
      const postData = { type: qType, uid: uId };
      this.backend
        .delete('/api/user/queue', postData)
        .then((resp) => {
          if (resp) {
            console.log(resp.message);
            this.userInfo.queue.splice(index, 1);
          }
        })
        .catch(err => console.error(err));
    },
    saveQueue(local = false) {
      const queue = JSON.parse(localStorage.getItem('queue'));
      this.backend
        .post('/api/user/queue', queue)
        .then((resp) => {
          let qId = null;
          if (resp) {
            localStorage.removeItem('queue');
            qId = resp.queue_id;
          }
          return qId;
        })
        .catch(err => console.error(err))
        .finally(() => {
          if (!local) {
            setTimeout(() => {
              location.reload();
            }, 1000);
          }
        });
    },
    clearDownloads() {
      const postData = { type: 'all' };
      this.backend
        .delete('/api/user/downloads', postData)
        .then((resp) => {
          if (resp) {
            console.log(resp.message);
            this.userInfo.downloads = null;
          }
        })
        .catch(err => console.error(err));
    },
    clearDownload(index, dUid) {
      const postData = { type: 'one', uid: dUid };
      this.backend
        .delete('/api/user/downloads', postData)
        .then((resp) => {
          if (resp) {
            console.log(resp.message);
            this.userInfo.downloads.splice(index, 1);
          }
        })
        .catch(err => console.error(err));
    },
  },
  computed: {
    userFormValid() {
      if (this.userInfo.profile.username) {
        return this.userInfo.profile.username.length > 5 || this.userInfo.profile.username === 'admin';
      }
      return false;
    },
  },
  created() {
    this.retrieveUserInfo();
  },
};
</script>
<style lang="scss" scoped>
@import "@/assets/variables.scss";

.user{
  .block {
    &_info {
      display: flex;
      align-items: center;
      justify-content: space-between;
      color: $white;
      margin-bottom: 15px;
    }
    &_title{
      font-family: $title-font;
      font-weight: bold;
      font-size: 22px;
    }
    &_action{
      font-family: $text-font;
      font-size: 15px;
      & > a {
        margin-right: 10px;
        &:last-child{
          margin-right: 0;
        }
      }
      .base_link{
        opacity: .6;
        cursor: pointer;
        &:hover{
          opacity: 1;
        }
      }
    }
  }
  &_profile{
    margin-bottom: 15px;
    .btn_exit{
      margin-top: 10px;
      background: $chilly;
    }
    &-form{
      display: flex;
      justify-content: space-between;
      .rightside{
        width: 48%;
        .form_row{
          display: flex;
          width: 100%;
          justify-content: space-between;
          .input_wide{
            width: 49%;
          }
        }
      }
      .leftside{
        width: 48%;
        padding: 10px;
        border-radius: 10px;
        .form_row{
          display: flex;
          justify-content: space-between;
          margin-bottom: 10px;
        }
        .form_column{
          height: auto;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          .input_wide{
            height: 25px;
          }
          & > div, & > .input_wide{
            margin-bottom: 0;
          }
          &.small {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 150px;
          }
          width: calc(100% - 160px);
        }
        .avatar{
          &_image{
            width: 120px;
            height: 120px;
          }
        }
      }
    }
  }
  &_history, &_queue{
    margin-bottom: 20px;
  }
  &_history{
    &-wrapper{
      display: flex;
      overflow: hidden;
      user-select: none;
      .history{

        &_item{
          margin-right: 10px;
          &:last-child{
            margin-right: 0;
          }
          &-card{
            border-radius: 8px;
            position: relative;
            margin-bottom: 5px;
            &:hover .history_item-actions{
              visibility: visible;
            }
            .preview_image{
              max-width: 250px;
            }
          }
          &-actions{
            background: rgba($color: #000000, $alpha: .4);
            position: absolute;
            visibility: hidden;
            border-radius: 8px;
            height: calc(100% - 3px);
            width: 100%;
            left: 0;
            top: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            .delete_button{
              border-radius: 50%;
              background: $blue;
              font-size: 20px;
              color: $white;
              height: 40px;
              width: 40px;
              display: flex;
              align-items: center;
              justify-content: center;
              cursor: pointer;
            }
          }
          &-info{
            color: $white;
            max-width: 240px;
            margin: 0 auto;
            .info_name{
              font-family: $title-font;
              margin-bottom: 5px;
            }
            .info_additional{
              font-family: $text-font;
              overflow: hidden;
              max-width: 235px;
              text-overflow: ellipsis;
              white-space: nowrap;
            }
          }
        }
      }
    }
  }
  &_queue{
    &-wrapper{
      display: flex;
      overflow: hidden;
    }
    .queue{
      &_item{
        margin-right: 10px;
        &:last-child{
          margin-right: 0;
        }
      }
      &_card{
        font-family: $text-font;
        border-radius: 8px;
        position: relative;
        margin-bottom: 5px;
        &:hover .queue_actions{
          visibility: visible;
        }
        .preview_image {
          max-width: 250px;
        }
      }
      &_actions{
        background: rgba(0, 0, 0, .4);
        visibility: hidden;
        border-radius: 8px;
        position: absolute;
        height: calc(100% - 3px);
        width: 100%;
        left: 0;
        top: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        .download_button{
          margin-right: 10px;
        }
        .download_button, .clear_button{
          border-radius: 50%;
          background: $blue;
          font-size: 20px;
          color: $white;
          height: 40px;
          width: 40px;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
        }
        .runtime {
          visibility: visible;
          position: absolute;
          right: 5px;
          bottom: 10px;
          padding: 3px 5px;
          background: rgba($color: #000000, $alpha: .3);
          color: $white;
          font-size: 14px;
          border-radius: 3px;
          text-shadow: $text-shadow;
        }
      }
      &_info{
        user-select: none;
        color: $white;
        max-width: 240px;
        margin: 0 auto;
        cursor: default;
        .info_data{
          margin-bottom: 5px;
          font-family: $title-font;
        }
        .info_name{
          font-family: $text-font;
          white-space: nowrap;
          overflow: hidden;
          max-width: 235px;
          text-overflow: ellipsis;
        }
      }
    }
  }
  &_downloads{
    &-wrapper{
      display: flex;
      overflow: hidden;
    }
    .downloads{
      margin-right: 10px;
      font-family: $text-font;
      color: $white;
      &:last-child{
        margin-right: 0;
      }
      &_card{
        border-radius: 8px;
        position: relative;
        margin-bottom: 5px;
        &:hover .downloads_actions{
          visibility: visible;
        }
        .preview_image{
          max-width: 250px;
        }
      }
      &_actions{
        visibility: hidden;
        background: rgba($color: #000000, $alpha: .4);
        .base_button{
          margin-right: 10px;
          .ms-icon{
            margin-left: 4px;
          }
        }
        .clear_button, .base_button{
          line-height: 40px;
          border-radius: 50%;
          background-color: $blue;
          font-size: 20px;
          color: $white;
          height: 40px;
          width: 40px;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
        }
        .runtime{
          visibility: visible;
          position: absolute;
          right: 5px;
          bottom: 10px;
          padding: 4px 5px;
          background: rgba($color: #000000, $alpha: .3);
          color: $white;
          font-size: 14px;
          border-radius: 3px;
          text-shadow: $text-shadow;
        }
      }
      &_actions, &_progress{
        border-radius: 8px;
        position: absolute;
        height: calc(100% - 3px);
        width: 100%;
        left: 0;
        top: 0;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      &_progress{
        background: rgba($color: #000000, $alpha: .6);
        .progress_bar{
          width: 60px;
          height: 60px;
          &-count{
            width: 100%;
            height: 100%;
            border-radius: 50%;
          }
        }
      }
      &_info{
        max-width: 240px;
        .info_data{
          font-family: $title-font;
        }
      }
    }
  }
}
</style>



