<template>
  <article class="watch page_wrapper">
    <div class="page_content" v-if="media">
      <div class="page_bg" v-if="media.backdrop">
        <img :src="`/i/${media.backdrop}`" class="base_image">
      </div>
      <div class="watch_logo" @click="redirect(`/${media.uid}`)">
        <img :src="`/i/${media.logo}`" class="base_image" v-if="media.logo">
      </div>
      <div class="watch_info">
        <div class="episode_name">{{ media.episode.name }}</div>
        <div class="episode_info">
          {{ media.episode.season }}  {{ media.episode.episode }} 
        </div>
      </div>
      <div class="watch_player">
        <div class="prev_episode" v-if="media.prev_episode"
          @click="redirect(`/watch/${media.prev_episode.uid}`)">
          <div class="episode_caption">
            <span class="episode_name">{{ media.prev_episode.name }}</span><br>
            {{ media.prev_episode.episode }} 
          </div>
        </div>
        <div class="loading" v-if="loading">
          <svg width="32px" height="24px">
            <polyline id="back" points="1 6 4 6 6 11 10 1 12 6 15 6"></polyline>
            <polyline id="front" points="1 6 4 6 6 11 10 1 12 6 15 6"></polyline>
          </svg>
        </div>
        <msplayer :videoSource="media.episode.meta.video_source"
          :preview="`/i/${media.episode.preview}`" :title="media.episode.title"
          :subtitle="media.episode.meta.sub" :subLang="media.episode.meta.sub_lang"
          :continue="media.episode.continue" :local="media.episode.meta.downloaded"
          :filename="media.episode.meta.filename" ref="MSPlayer" @unload="sendPlayerData" />
        <div class="next_episode" v-if="media.next_episode"
          @click="redirect(`/watch/${media.next_episode.uid}`)">
          <div class="episode_caption">
            <span class="episode_name">{{ media.next_episode.name }}</span><br>
            {{ media.next_episode.episode }} 
          </div>
        </div>
      </div>
      <div class="watch_overview">
        <div class="overview_title"></div>
        <p class="overview">{{ media.episode.overview }}</p>
      </div>
    </div>
  </article>
</template>
<script>
import msplayer from '@/components/Player';
import Exchange from '../services/exchange.service';

export default {
  name: 'Watch',
  components: { msplayer },
  data() {
    return {
      backend: new Exchange(),
      uid: null,
      media: null,
      loading: true,
      extra: null,
      video: null,
      params: {
        continue: null,
      },
    };
  },
  methods: {
    retrieveMedia() {
      const params = {
        nghbr: true,
      };
      this.backend
        .get(`/api/episode/${this.uid}`, params)
        .then((resp) => {
          if (resp) {
            this.media = resp.body;
            this.extra = resp.extra;
            console.log(this.media);
          }
        })
        .catch(err => console.log(err))
        .finally(() => {
          this.$refs.MSPlayer.initPlayer();
        });
    },
    redirect(url) {
      location.href = url;
    },
    sendPlayerData(playerData) {
      const data = {
        player: playerData,
        mediaType: this.media.media_type,
        uid: this.uid,
      };
      if (playerData.timestamp && playerData.duration) {
        this.backend.beacon(data);
      }
    },
  },
  created() {
    this.uid = this.$route.params.uid;
    this.retrieveMedia();
    this.params.continue = this.$route.query.c;
  },
};
</script>
<style lang="scss" scoped>
@import "@/assets/variables.scss";

.watch{
  .page_content{
    position: relative;
    .page_bg{
      position: absolute;
      z-index: -1;
      .base_image{
        z-index: 0;
      }
      &:after{
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: radial-gradient(50% 50% at 50% 50%, rgba(29, 29, 29, 0.58) 0%, #1D1D1D 100%);
      }
    }
  }
  &_logo{
    height: 120px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px 0;
  }
  &_info{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 12px;
    color: $white;
    font-family: $text-font;
    .episode_name{
      font-family: $title-font;
      font-weight: bold;
      font-size: 22px;
    }
  }
  &_player{
    width: 100%;
    position: relative;
    aspect-ratio: 16 / 9;
    background: rgba(0, 0, 0, .3);
    border-radius: 10px;
    margin-bottom: 10px;
    .prev_episode,
    .next_episode{
      visibility: hidden;
      height: 45px;
      position: absolute;
      top: calc(50% - 22.5px);
      border-radius: 30px;
      background: $blue;
      display: flex;
      align-items: center;
      justify-content: center;
      color: $white;
      font-size: 14px;
      font-family: $title-font;
      cursor: pointer;
      .episode_name{
        font-size: 16px;
        font-family: $text-font;
        font-weight: bold;
      }
      &:hover{
        visibility: visible;
      }
    }
    .prev_episode{
      left: -20px;
      padding: 5px 20px 5px 0;
      z-index: 1;
      &:before{
        visibility: visible;
        font-family: 'FontAwesome';
        font-size: 20px;
        width: 45px;
        height: 45px;
        content: '\f053';
        display: flex;
        align-items: center;
        justify-content: center;
        background: $dark-grey;
        border-radius: 50%;
        color: $white;
        margin-right: 10px;
      }
    }
    .next_episode{
      right: -20px;
      padding: 5px 0 5px 20px;
      text-align: right;
      &:after{
        content: '\f054';
        visibility: visible;
        font-family: 'FontAwesome';
        font-size: 20px;
        width: 45px;
        height: 45px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: $dark-grey;
        border-radius: 50%;
        color: $white;
        margin-left: 10px;
      }
    }
  }
  &_overview{
    .overview_title{
      font-family: $title-font;
      font-size: 24px;
      text-shadow: $text-shadow;
      font-weight: bold;
      margin-bottom: 10px;
    }
    padding: 0 10px;
    font-family: $text-font;
    color: $white;
    text-align: justify;

  }
}
</style>



