<template>
  <article class="edit_element page_wrapper">
    <div class="loading" v-if="loading">
      <svg width="32px" height="24px">
        <polyline id="back" points="1 6 4 6 6 11 10 1 12 6 15 6"></polyline>
        <polyline id="front" points="1 6 4 6 6 11 10 1 12 6 15 6"></polyline>
      </svg>
    </div>
    <div class="page_content" v-else>
      <div class="page_bg">
        <img :src="`/i/${media.backdrop}`" class="base_image">
      </div>
      <div class="page_logo">
        <img :src="`/i/${media.logo}`" class="base_image" v-if="media.logo">
      </div>
      <div class="page_container">
        <div class="page_container-left">
          <div class="images" v-for="(image, index) in imageContents" :key="index">
            <div :class="`${image}_image`">
              <div :class="`${image}_image-img`">
                <file :image="image" preview :name="image" draggable
                  :existing="`/i/${media[image]}`" v-model="movieInfo[image]"
                  :class="`${image}_file`" v-if="media[image]"/>
                <file :image="image" preview :name="image" draggable
                  v-model="movieInfo[image]" :class="`${image}_file`" v-else/>
              </div>
            </div>
          </div>
        </div>
        <div class="page_container-right">
          <div class="movie_meta" v-if="media.media_type === 'movie'">
            <div class="block_title"> </div>
            <form class="edit_movie-meta" @submit.prevent="handleEditMeta">
              <input type="text" placeholder=" " class="input_wide"
                v-model="movieMeta.videoSource">
              <div class="voices" v-if="voiceLoading || voices || movieMeta.voiceLang">
                <div class="voice_object input_wide" v-if="voiceLoading">
                   ..
                </div>
                <selecter :options="voices" v-show="voices" multiple updatable
                  placeholder=" " v-model="movieMeta.videoLang"
                  ref="voiceSelection" />
              </div>
              <div class="voices" v-if="!voices && !voiceLoading">
                <selecter :options="defaultLangs" multiple ref="voiceSelection"
                  placeholder=" " v-model="movieMeta.videoLang" />
              </div>
              <transition name="toggle">
                <div class="subtitles_wrapper" v-if="movieMeta.videoLang === '20'">
                  <input type="text" class="input_wide" placeholder=" "
                    v-model="movieMeta.sub">
                  <selecter :options="extra.langs" multiple placeholder=" "
                    v-model="movieMeta.subLang" :defaultValue="movieMeta.subLang"
                    updatable ref="subtitlesSelection" />
                </div>
              </transition>
              <button type="submit" class="btn btn_submit"
                ref="editMetaButton"></button>
            </form>
            <div class="auto_meta" v-if="voices">
              <button type="button" class="btn btn_submit autometa"
                @click="parseMeta" ref="autoMetaButton"> </button>
              <div class="auto_meta-caption">     </div>
            </div>
          </div>
          <div class="movie_info">
            <div class="block_title"> </div>
            <form @submit.prevent="handleEditInfo">
              <input type="text" required class="input_wide" placeholder=""
                v-model="movieInfo.name">
              <input type="text" class="input_wide" required
                placeholder=" " v-model="movieInfo.originalName">
              <textarea class="input_wide textarea_wide" placeholder=""
                v-model="movieInfo.overview"></textarea>
              <button type="submit" class="btn btn_submit"
                ref="editInfoButton"></button>
            </form>
          </div>
        </div>
      </div>
    </div>
  </article>
</template>
<script>
import fileUpload from '@/components/FileUploader';
import selecter from '@/components/Selecter';
import Exchange from '../services/exchange.service';

export default {
  name: 'EditElement',
  components: { file: fileUpload, selecter },
  data() {
    return {
      backend: new Exchange(),
      iud: null,
      loading: true,
      extra: null,
      voiceLoading: true,
      voices: null,
      imageContents: ['poster', 'backdrop', 'logo'],
      defaultLangs: null,
      movieMeta: {
        videoSource: null,
        videoLang: null,
        sub: null,
        subLang: null,
      },
      movieInfo: {
        name: null,
        originalName: null,
        overview: null,
        logo: null,
        poster: null,
        backdrop: null,
      },
    };
  },
  methods: {
    retrieveMedia() {
      this.backend
        .get(`/api/media/${this.uid}`)
        .then((resp) => {
          if (resp) {
            this.media = resp.body;
            this.extra = resp.extra;
          }
        })
        .catch(err => console.error(err))
        .finally(() => {
          this.loading = false;
          this.applyData();
          this.retrieveVoices();
        });
    },
    applyData() {
      this.movieInfo.name = this.media.name;
      this.movieInfo.originalName = this.media.original_name;
      this.movieInfo.overview = this.media.overview;
      if (this.media.meta) {
        this.movieMeta.videoSource = this.media.meta.video_source;
        this.movieMeta.videoLang = this.media.meta.video_lang;
        this.movieMeta.sub = this.media.meta.sub;
        this.movieMeta.subLang = this.media.meta.sub_lang;
      }
    },
    retrieveVoices() {
      const params = {
        imdb_id: this.media.imdb_id,
        media_type: this.media.media_type,
      };
      this.backend
        .get(`/api/voices/${this.media.media_type}`, params)
        .then((resp) => {
          if (resp) {
            this.voices = resp.body.results;
          }
        })
        .catch(err => console.error(err))
        .finally(() => {
          this.voiceLoading = false;
        });
    },
    handleEditMeta() {
      let saved = false;
      const prevText = this.$refs.editMetaButton.textContent;
      this.$refs.editMetaButton.textContent = '';
      this.backend
        .post(`/api/meta/edit/${this.media.media_type}/${this.uid}`, this.movieMeta)
        .then((resp) => {
          if (resp) {
            saved = true;
          }
        })
        .catch(err => console.error(err))
        .finally(() => {
          this.$refs.editMetaButton.textContent = prevText;
          if (saved) {
            setTimeout(() => {
              location.href = `/${this.media.uid}`;
            }, 1500);
          }
        });
    },
    parseMeta() {
      const params = {
        voice: this.movieMeta.videoLang,
        sub: this.movieMeta.subLang,
      };
      const prevText = this.$refs.autoMetaButton.textContent;
      this.$refs.autoMetaButton.textContent = '';
      this.backend
        .get(`/api/meta/${this.media.media_type}/${this.media.imdb_id}`, params)
        .then((resp) => {
          if (resp) {
            this.movieMeta.videoSource = resp.results.video_src;
            this.movieMeta.sub = resp.results.sub_src;
          }
        })
        .catch(err => console.error(err))
        .finally(() => {
          this.$refs.autoMetaButton.textContent = prevText;
        });
    },
    handelEditInfo() {
      let saved = false;
      const formData = new FormData();
      Object.keys(this.movieInfo).forEach(key => formData.append(key, this.episodeMeta[key]));
      const prevText = this.$refs.editInfoButton.textContent;
      this.$refs.editInfoButton.textContent = '';
      this.backend
        .post(`/api/${this.media.uid}/`, formData)
        .then((resp) => {
          if (resp) {
            saved = true;
          }
        })
        .finally(() => {
          this.$refs.editInfoButton.textContent = prevText;
          if (saved) {
            setTimeout(() => {
              location.href = `/${this.media.uid}`;
            }, 1500);
          }
        });
    },
  },
  created() {
    this.uid = this.$route.params.uid;
    this.retrieveMedia();
  },
};
</script>
<style lang="scss" scoped>
@import "@/assets/variables.scss";
.edit_element{
  position: relative;
  .page{
    &_content{
      width: 100%;
    }
    &_bg{
      position: absolute;
      width: 100%;
      z-index: -1;
      &:after{
        position: absolute;
        content: '';
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background: radial-gradient(50% 50% at 50% 50%, rgba(29, 29, 29, 0.58) 0%, #1D1D1D 100%);
      }
    }
    &_logo{
      height: 120px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 10px 0;
    }
    &_container{
      display: flex;
      align-items: center;
      justify-content: space-around;
      &-left{
        max-width: 200px;
        .poster_image{
          position: relative;
          margin-bottom: 5px;
          &:hover .poster_image-actions{
          visibility: visible;
          opacity: 1;
          }
          &-img{
            border-radius: 10px;
            .poster_file{
              max-width: 200px;
              aspect-ratio: 2 / 3;
            }
          }
          &-actions{
            position: absolute;
            right: 10px;
            top: 5px;
            visibility: hidden;
            opacity: 0;
            transition: all .3s ease;
            .delete_preview{
              color: $white;
              background: $blue;
              border-radius: 50%;
              padding: 5px;
              cursor: pointer;
            }
          }
        }
        .backdrop_image{
          position: relative;
          margin-bottom: 5px;
          &:hover .backdrop_image-actions{
            visibility: visible;
            opacity: 1;
          }
          &-img{
            border-radius: 10px;
          }
          &-actions{
            position: absolute;
            right: 10px;
            top: 5px;
            visibility: hidden;
            overflow: 0;
            transition: all .3s ease;
            .delete_preview{
              color: $white;
              background: $blue;
              border-radius: 50%;
              padding: 5px;
              cursor: pointer;
            }
          }
        }
        .logo_image{
          position: relative;
          margin-bottom: 5px;
          &:hover .logo_image-actions{
            visibility: visible;
            opacity: 1;
          }
          &-img{
            border-radius: 10px;
          }
          &-actions{
            position: absolute;
            right: 10px;
            top: 5px;
            visibility: hidden;
            overflow: 0;
            transition: all .3s ease;
            .delete_preview{
              color: $white;
              background: $blue;
              border-radius: 50%;
              padding: 5px;
              cursor: pointer;
            }
          }
        }
        .movie_name{
          color: $white;
          font-family: $text-font;
          max-width: 190px;
          text-align: center;
          font-size: 16px;
          margin: 0 auto;
        }
      }
      &-right{
        font-family: $text-font;
        .block_title{
          color: $white;
          margin-bottom: 10px;
          text-align: center;
          font-size: 18px;
        }
        .movie_meta{
          margin-bottom: 20px;
          .voices{
            .voice_object{
              line-height: 30px;
              color: $dark-grey;
            }
          }
          .auto_meta{
            margin-top: 10px;
            font-family: $text-font;
            .autometa{
              background: $dark;
              &:hover + .auto_meta-caption{
                color: $white;
              }
            }
            &-caption{
              margin: 5px 0 0 5px;
              color: rgba(217, 217, 217, .7);
              text-shadow: $text-shadow;
            }
          }
        }
        .movie_info{
          max-width: 450px;
          .info_files{
            display: flex;
          }
        }
      }
    }
  }
}
.toggle-enter-to, .toggle-leave{
  max-height: 70px;
  opacity: 1;
}
.toggle-enter-active, .toggle-leave-active {
  transition: all .45s ease-in-out;
}
.toggle-enter, .toggle-leave-to {
  max-height: 0;
  opacity: 0;
}
</style>



