<template>
  <div class="file" :style="cssStyles">
    <div class="file_upload"
      v-show="!uploaded" :class="`${dragStyle} ${image}`"
      @drop.prevent="handleDrop"
      @dragleave.prevent="handleDragEnd"
      @dragover.prevent="handleDragStart">
      <label :for="`imageInput-${image}`" class="file_upload-label">
        <span class="default_image">
          <i class="ms-icon image wh-30"></i>
        </span>
        <span class="label_text"> </span>
        <span class="label_caption"
          v-if="image !== 'avatar'">      </span>
      </label>
      <input type="file" class="image_input"
        :id="`imageInput-${image}`" ref="fileInput" :name="name"
        @change="makePreview">
    </div>
    <div class="file_preview" v-show="uploaded && preview">
      <div class="file_preview-actions">
        <div class="delete_selected" title=" " @click="deleteSelected">
          <i class="ms-icon delete wh-20"></i>
        </div>
      </div>
      <img :src="imagePreview" class="file_preview-image base_image">
    </div>
  </div>
</template>
<script>
export default {
  name: 'fileUpload',
  props: {
    preview: {
      type: Boolean,
      default: false,
    },
    image: {
      type: String,
      default: 'preview',
    },
    name: {
      type: String,
    },
    draggable: {
      type: Boolean,
      default: true,
    },
    existing: {
      type: String,
    },
    color: {
      type: String,
      default: '#e8f1f233',
    },
  },
  data() {
    return {
      dragStyle: '',
      imageUrl: '',
      uploaded: false,
      defaultColor: '#e8f1f233',
      hoverBg: '59',
      file: null,
      imagePreview: null,
    };
  },
  methods: {
    handleDrop() {
      const input = this.$refs.fileInput;
      input.files = event.dataTransfer.files;
      this.dragStyle = '';
      if (this.preview) {
        this.makePreview();
      }
    },
    handleDragEnd() {
      this.dragStyle = '';
    },
    handleDragStart() {
      this.dragStyle = 'active';
    },
    makePreview() {
      const input = this.$refs.fileInput;
      if (input.files && input.files[0]) {
        this.imagePreview = URL.createObjectURL(input.files[0]);
        this.uploaded = true;
        this.file = input.files[0];
        this.$emit('input', this.file);
      }
    },
    makeExisting(url) {
      this.imagePreview = `${url}`;
      this.uploaded = true;
    },
    deleteSelected() {
      this.imagePreview = null;
      this.file = null;
      this.uploaded = false;
    },
    styleReplacer(str, add) {
      const arr = str.split('');
      const strLast = arr.length - 2;
      if (arr.length === 7) {
        arr.push('5', '9');
      } else {
        arr.splice(strLast, 2, add);
      }
      return arr.join('');
    },
  },
  computed: {
    cssStyles() {
      return {
        '--bg-color': this.color,
        '--hover-bg': this.color ? this.styleReplacer(this.color, this.hoverBg)
          : this.styleReplacer(this.defaultColor, this.hoverBg),
      };
    },
  },
  created() {
    if (this.existing) {
      this.makeExisting(this.existing);
    }
  },
};
</script>
<style lang="scss" scoped>
@import "@/assets/variables.scss";

.file{
  &_upload{
    backdrop-filter: blur(5px);
    border-radius: 10px;
    transition: all .25s ease;
    background: var(--bg-color);
    &:hover {
      background: var(--hover-bg);
    }
    &.active{
      background: var(--hover-bg);
    }
    &.poster{
      height: 300px;
      aspect-ratio: 2 / 3;
    }
    &.preview{
      width: 300px;
      aspect-ratio: 16 / 9;
    }
    &.avatar{
      width: 120px;
      aspect-ratio: 1 / 1;
    }
    &-label{
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      padding: 10px 15px;
      box-sizing: border-box;
      font-family: $text-font;
      font-size: 14px;
      color: $white;
      opacity: 0.5;
      cursor: pointer;
      .default_image{
        color: $white;
        height: 40%;
        font-size: 30px;
        text-shadow: $text-shadow;
        display: flex;
        align-items: flex-end;
        justify-content: center;
        margin-bottom: 5px;
      }
      .label_text{
        width: 85%;
        text-align: center;
        margin-bottom: 5px;
      }
      .label_caption{
        width: 85%;
      }
    }
    .image_input{
      display: none;
    }
  }
  &_preview{
    position: relative;
    &:hover .file_preview-actions .delete_selected{
        opacity: 1;
        visibility: visible;
      }
    &-actions{
      .delete_selected{
        opacity: 0;
        visibility: hidden;
        position: absolute;
        top: 5px;
        right: 10px;
        background: $blue;
        color: $white;
        border-radius: 50%;
        cursor: pointer;
        padding: 5px;
        transition: all .4s ease;
      }
    }
    &-image{
      border-radius: 10px;
    }
  }
}
</style>



