import Vue from 'vue';
import Router from 'vue-router';
import store from '@/store';
import Index from '@/views/Index';
import Auth from '@/views/Auth';
import Search from '@/views/Search';
import MediaElements from '@/views/MediaElements';
import MediaElement from '@/views/MediaElement';
import EditMeta from '@/views/EditMeta';
import EditElement from '@/views/EditElement';
import Watch from '@/views/Watch';
import User from '@/views/User';

Vue.use(Router);

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: '',
      component: Index,
    },
    {
      path: '/sign-in',
      name: '',
      component: Auth,
    },
    {
      path: '/search',
      name: ' ',
      component: Search,
    },
    {
      path: '/tv',
      name: '',
      meta: {
        mediaType: 'tv',
      },
      component: MediaElements,
    },
    {
      path: '/movie',
      name: '',
      meta: {
        mediaType: 'movie',
      },
      component: MediaElements,
    },
    {
      path: '/user',
      name: '',
      component: User,
    },
    {
      path: '/watch/:uid',
      name: '',
      component: Watch,
    },
    {
      path: '/edit/:uid',
      name: ' ',
      component: EditMeta,
    },
    {
      path: '/edit/movie/:uid',
      name: ' ',
      component: EditElement,
    },
    {
      path: '/:uid',
      name: '',
      component: MediaElement,
    },
  ],
});

router.beforeEach((to, from, next) => {
  const isAuth = store.getters.isAuthenticated;
  if (to.matched.some(route => route.meta.requiresAuth) && !isAuth) {
    next('/sign-in');
  } else {
    next();
  }
});

export default router;



