<template>
  <article class="auth page_wrapper">
    <div class="auth_header"></div>
    <div class="page_content">   !</div>
    <div class="auth_wrapper" v-show="showLogin">
      <div class="container_title"></div>
      <form class="auth_form" @submit.prevent="handleLogin">
        <input type="text"
          class="input_wide"
          required
          autocomplete="off"
          placeholder=""
          autofocus
          v-model="user.username">
        <input type="password"
          class="input_wide"
          required
          autocomplete="off"
          placeholder=""
          v-model="user.password">
        <div class="form_row">
          <a class="base_link" @click="showLostForm"> ?</a>
        </div>
        <button type="submit" class="btn btn_submit" ref="loginButton"></button>
      </form>
    </div>
    <div class="lostpassword_wrapper" v-show="showLost">
      <div class="container_title"> </div>
      <form class="lost_form" @submit.prevent="handleLost">
        <input type="text"
          class="input_wide"
          required
          autocomplete="off"
          placeholder="-mail"
          v-model="lostPassword.email">
        <button type="submit" class="btn btn_submit"></button>
      </form>
    </div>
  </article>
</template>
<script>
export default {
  name: 'Auth',
  data() {
    return {
      user: {
        username: null,
        password: null,
      },
      lostPassword: {
        email: null,
      },
      showLogin: true,
      showLost: false,
    };
  },
  methods: {
    handleLogin() {
      this.$store.dispatch('login', this.user)
        .then((resp) => {
          console.log(resp);
          location.href = '/';
        });
    },
    handleLost() {},
    showLostForm() {},
  },
  mounted() {
    if (this.$store.state.isAuthenticated) {
      location.href = '/';
    }
  },
  created() {},
};
</script>
<style lang="scss" scoped>
@import "@/assets/variables.scss";
.auth_header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 170px;
  background: url('/static/page_header.png') no-repeat 50% 50% / cover;
}
.page_content{
  text-align: center;
  color: $white;
  font-family: $title-font;
  font-size: 22px;
  margin-bottom: 20px;
}
.auth{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  flex-direction: column;
  &_wrapper{
    padding: 20px 30px;
    background: $white;
    border-radius: 10px;
    width: 500px;
  }
  &_form{
    display: flex;
    flex-direction: column;
    width: 100%;
    font-family: $text-font;
    .form_row{
      margin-bottom: 10px;
      margin-left: 10px;
      color: $blue;
      font-weight: bold;
    }
  }
}
</style>



