<script>
import selecter from '@/components/Selecter';
import msplayer from '@/components/Player';
import Exchange from '../services/exchange.service';

export default {
  name: 'MediaElement',
  components: { selecter, msplayer },
  data() {
    return {
      backend: new Exchange(),
      uid: null,
      message: null,
      media: null,
      extra: null,
      currentSeason: null,
      episodeForm: false,
      activeSeason: null,
      loading: true,
      showPlayer: false,
      player: null,
      video: null,
      playerInfo: {
        duration: null,
        time: null,
      },
      params: {
        continue: null,
      },
      episodeInfo: {
        mediaType: 'tv',
        season: null,
        episode: null,
      },
    };
  },
  methods: {
    retreiveMediaInfo() {
      this.backend
        .get(`/api/media/${this.uid}`)
        .then((resp) => {
          if (resp) {
            this.media = resp.body;
            this.extra = resp.extra;
          } else {
            console.log(this.backend.msg);
          }
        })
        .catch(err => console.error(err))
        .finally(() => {
          console.log(this.media);
          this.loading = false;
          this.showPlayer = true;
          if (!this.activeSeason && this.media.media_type === 'tv') {
            this.activeSeason = this.detectSeason();
          }
          document.title = `${this.media.name}  MediaServer`;
          if (this.media.media_type === 'movie' && this.media.meta) {
            this.$refs.MSPlayer.initPlayer();
          }
        });
    },
    paintRate(value) {
      let style = 'color: #777777';
      if (value && value !== '-') {
        const percent = parseInt(value * 10, 10);
        if (percent < 49) {
          style = 'color: #ff0000';
        } else if (percent > 70) {
          style = 'color: #3bb33b';
        }
      }
      return style;
    },
    makePluralSeasons(count) {
      const array = ['', '', ''];
      const ending = array[(count % 100 > 4 && count % 100 < 20)
        ? 2 : [2, 0, 1, 1, 1, 2][(count % 10 < 5) ? count % 10 : 5]];
      return `${count} ${ending}`;
    },
    makePluralEpisodes(count) {
      const array = ['', '', ''];
      const ending = array[(count % 100 > 4 && count % 100 < 20)
        ? 2 : [2, 0, 1, 1, 1, 2][(count % 10 < 5) ? count % 10 : 5]];
      return `${count} ${ending}`;
    },
    showEpisodeForm() {
      this.episodeForm = !this.episodeForm;
    },
    markAsSeen() {
      const prevButton = this.$refs.seenButton.innerHTML;
      this.backend
        .post(`/api/mark-seen/${this.uid}`)
        .then((resp) => {
          if (resp) {
            this.$refs.seenButton.innerHTML = '<i class="ms-icon check wh-20"></i>';
          }
        })
        .catch(err => console.error(err))
        .finally(() => {
          setTimeout(() => {
            this.$refs.seenButton.innerHTML = prevButton;
          }, 2000);
        });
    },
    transformScroll() {
      if (!event.deltaY) {
        return;
      }
      let scrollSize = 0;
      if (event.deltaX !== 0) {
        scrollSize = event.deltaX;
      } else if (event.deltaY !== 0) {
        scrollSize = event.deltaY;
      }
      this.$refs.eWrap.scrollLeft += scrollSize;
    },
    changeSeasonMeta() {
      if (this.episodeInfo.season) {
        const seasonsMeta = this.media.seasons_meta.split(',');
        this.$refs.episodeInput.max = seasonsMeta[this.episodeInfo.season - 1];
      }
    },
    addNewEpisode() {
      this.episodeInfo.mediaUid = this.media.uid;
      this.episodeInfo.tmdb_id = this.media.tmdb_id;
      this.episodeInfo.kp_id = this.media.kp_id;
      this.episodeInfo.imdb_id = this.media.imdb_id;
      const prevText = this.$refs.episodeButton.textContent;
      this.$refs.episodeButton.textContent = '';
      this.backend
        .post('/api/episode/', this.episodeInfo)
        .then((resp) => {
          if (resp) {
            this.message = resp.message;
          }
        })
        .catch(err => console.error(err))
        .finally(() => {
          this.$refs.episodeButton.textContent = prevText;
          this.episodeForm = false;
          this.activeSeason = this.episodeInfo.season;
          this.episodeInfo.season = null;
          this.$refs.selection.clearSelection();
          this.episodeInfo.episode = null;
          this.retreiveMediaInfo();
          this.scrollToLastElement();
        });
    },
    downloadEl(uId) {
      let aDownloads = JSON.parse(localStorage.getItem('aDownloads'));
      const downloadInfo = {
        media_type: this.media.media_type,
        uid: uId,
      };
      this.backend
        .post('/api/download/', downloadInfo)
        .then((resp) => {
          if (resp) {
            downloadInfo.download_id = resp.body.uid;
            if (aDownloads) {
              aDownloads.push(downloadInfo);
            } else {
              aDownloads = [downloadInfo];
            }
          }
        })
        .catch(err => console.error(err))
        .finally(() => {
          localStorage.setItem('aDownloads', JSON.stringify(aDownloads));
        });
    },
    updateQueue(ep) {
      let queue = JSON.parse(localStorage.getItem('queue'));
      const queueEpisode = {
        name: this.media.name,
        title: ep.name,
        season: ep.season,
        episode: ep.episode,
        preview: ep.preview,
        filename: ep.meta.filename,
        uid: ep.uid,
      };
      queue = !queue ? [] : queue;
      queue.push({ type: 'episode', episode: queueEpisode });
      localStorage.setItem('queue', JSON.stringify(queue));
      window.dispatchEvent(new CustomEvent('queueUpdate', {
        detail: { storage: localStorage.getItem('queue') },
      }));
    },
    scrollToLastElement() {
      this.$refs.eWrap.scroll({ left: this.$refs.eWrap.scrollWidth, behavior: 'smooth' });
    },
    progressBar(time, runtime) {
      // eslint-disable-next-line
      return time ? `width: ${((time/(runtime * 60))*100)}%` : '';
    },
    detectSeason() {
      let currentSeason = null;
      if (this.currentSeason) {
        currentSeason = parseInt(this.currentSeason, 10);
      }
      if (!this.currentSeason && this.media.extra.length >= 1) {
        let minimalSeason = 1;
        minimalSeason = this.media.seasons_meta.length >= 1
          ? this.detectUnwatchedSeason()
          : Math.min(...this.media.season_items);
        currentSeason = minimalSeason;
      }
      return currentSeason;
    },
    detectUnwatchedSeason() {
      const unwatchedSeason = [];
      this.media.extra.filter((obj) => {
        if (!obj.seen) {
          unwatchedSeason.push(obj.season);
        }
        return obj.season;
      });
      return [...new Set(unwatchedSeason)][0];
    },
    changeSeason(season) {
      this.activeSeason = season;
      this.$router.replace({ query: { s: season } });
    },
    sendPlayerData(playerData) {
      const data = {
        player: playerData,
        mediaType: this.media.media_type,
        uid: this.uid,
      };
      this.backend.beacon(data);
    },
  },
  created() {
    this.currentSeason = this.$route.query.s;
    this.uid = this.$route.params.uid;
    this.params.continue = this.$route.query.c;
    this.retreiveMediaInfo();
  },
};
</script>
