import axios from 'axios';
import authHeader from './auth-header';


class UserService {
  async getMe() {
    this.msg = 'auth';
    if (localStorage.getItem('user')) {
      return axios
        .get('/auth/get-me/', { headers: authHeader('main') })
        .then((resp) => {
          if (resp.data.status === 'success' && resp.data.granted) {
            this.msg = 'granted';
          } else {
            localStorage.removeItem('user');
            this.msg = 'auth';
          }
          return this.msg;
        })
        .catch((err) => {
          if (err.response.status === 401) {
            if (err.response.data.msg === 'TNF') {
              this.msg = 'auth';
            } else if (err.response.data.msg === 'THE') {
              this.refresh();
              this.msg = 'refresh';
            }
          }
        });
    }
    return this.msg;
  }

  async refresh() {
    this.user = JSON.parse(localStorage.getItem('user'));
    return axios
      .post('/auth/refresh/', {}, { headers: authHeader('refresh') })
      .then((resp) => {
        if (resp.data.status === 'success') {
          this.user.accs_token = resp.data.token;
        }
        localStorage.setItem('user', JSON.stringify(this.user));
        location.reload();
      })
      .catch((err) => {
        console.log(err);
        if (err.response.data.msg === 'THE') {
          this.msg = 'auth';
          localStorage.removeItem('user');
          location.href = '/sign-in';
        }
      })
      .finally(() => this.msg);
  }
}

export default new UserService();



