<template>
  <article class="search page_wrapper">
    <div class="search_wrapper">
      <form class="global_search" @submit.prevent="retrieveResults">
        <input type="text"
          placeholder=" "
          class="input_wide"
          v-model="params.keyword"
          required>
        <button type="submit" class="btn btn_submit"></button>
      </form>
    </div>
    <div class="search_results" v-show="results">
      <div class="search_results-card"
        v-for="(result, index) in results" :key="result.tmdb_id">
        <div class="card_poster">
          <img :src="`${extra.imageStorage}${result.poster}`"
            :alt="`${result.original_name} poster`"
            class="bg_image">
        </div>
        <div class="card_info">
          <div class="card_bg">
            <img :src="`${extra.imageStorage}${result.backdrop}`"
              :alt="`${result.original_name} background`" class="bg_image">
          </div>
          <div class="card_name no_select">
            <i class="ms-icon wh-30" :class="result.media_type"></i>
            {{result.name}} ({{result.year}})
          </div>
          <div class="card_rating no_select">
            <div class="rater_object" :style="paintRate(result.tmdb_rate)">
              <div class="rate_name tmdb">TMDb</div>
              <div class="rate_value">{{result.tmdb_rate}}</div>
            </div>
            <div class="rater_object" :style="paintRate(result.kp_rate)">
              <div class="rate_name kp"></div>
              <div class="rate_value">{{result.kp_rate}}</div>
            </div>
          </div>
          <div class="card_overview no_select" :title="result.overview">
            {{result.overview}}
          </div>
          <div class="card_action" v-if="!checkDuplicate(result.tmdb_id)">
            <button type="button" class="btn btn_submit btn_add"
              ref="buttonAdd"
              @click="addNewItem(index)"></button>
          </div>
        </div>
      </div>
    </div>
    <div class="search_results-loading" v-show="loading">
      <div class="loading">
        <svg width="32px" height="24px">
          <polyline id="back" points="1 6 4 6 6 11 10 1 12 6 15 6"></polyline>
          <polyline id="front" points="1 6 4 6 6 11 10 1 12 6 15 6"></polyline>
        </svg>
      </div>
    </div>
  </article>
</template>
<script>
import Exchange from '@/services/exchange.service';

export default{
  name: 'Search',
  data() {
    return {
      backend: new Exchange(),
      params: {
        keyword: null,
      },
      results: null,
      extra: {
        imageStorage: null,
        duplicates: null,
      },
      currentItem: {},
      loading: false,
    };
  },
  methods: {
    retrieveResults() {
      if (this.params.keyword) {
        this.results = null;
        this.loading = true;
        this.backend
          .get('/api/search/', this.params)
          .then((resp) => {
            if (resp) {
              this.extra = resp.extra;
              this.results = resp.body;
            } else {
              console.log(this.backend.msg);
            }
          })
          .catch(err => console.error(err))
          .finally(() => {
            this.loading = false;
          });
      }
    },
    addNewItem(index) {
      const prevText = this.$refs.buttonAdd[index].textContent;
      const mediaType = this.results[index].media_type;
      this.$refs.buttonAdd[index].textContent = '';
      this.currentItem = this.results[index];
      let givenId = '';
      this.backend
        .post(`/api/${mediaType}/`, this.currentItem)
        .then((resp) => {
          if (resp) {
            givenId = resp.body;
            this.$refs.buttonAdd[index].textContent = prevText;
            this.results.splice(index, 1);
          } else {
            console.log(this.backend.msg);
          }
        })
        .catch(err => console.error(err))
        .finally(() => {
          if (this.results.length === 0) {
            location.href = givenId.uid;
          }
        });
    },
    paintRate(value) {
      let style = 'background: #777777;';
      if (value && value !== '-') {
        const percent = parseInt(value * 10, 10);
        if (percent < 49) {
          style = 'background: #ff0000';
        } else if (percent > 70) {
          style = 'background: #3bb33b';
        }
      }
      return style;
    },
    checkDuplicate(tmdbId) {
      if (this.extra.duplicates) {
        return this.extra.duplicates.includes(tmdbId);
      }
      return false;
    },
  },
  created() {
    this.params.keyword = this.$route.query.k;
    this.retrieveResults();
  },
};
</script>
<style lang="scss" scoped>
@import "@/assets/variables.scss";

.search{
  font-family: $text-font;
  &_wrapper{
    margin-bottom: 20px;
    .global_search{
      display: flex;
      justify-content: space-between;
      .input_wide{
        max-width: 85%;
        margin-bottom: 0;
      }
      .btn_submit{
        max-width: 100px;
        box-shadow: $block-shadow;
      }
    }
  }
  &_results{
    padding: 0 10px;
    &-card{
      width: 100%;
      display: flex;
      height: 300px;
      justify-content: space-between;
      margin-bottom: 20px;
      .card_poster{
        .bg_image{
          aspect-ratio: 2 / 3;
          height: 100%;
          border-radius: 5px;
        }

      }
      .card_info{
        display: flex;
        position: relative;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;
        width: calc(80% - 30px);
        padding: 20px 40px;
        color: $white;
        text-shadow: $text-shadow;
        .card_bg{
          width: 100%;
          height: 100%;
          top: 0;
          left: 0;
          position: absolute;
          z-index: -1;
          &:after{
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background: linear-gradient(to bottom, rgba(0, 0, 0, .5), rgba(0, 0, 0, .5));
            content: '';
            position: absolute;
            border-radius: 5px;
          }
          .bg_image{
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 5px;
          }
        }
        .card_name{
          display: flex;
          justify-content: center;
          align-items: center;
          font-family: $title-font;
          font-weight: bold;
          font-size: 20px;
          .ms-icon{
            margin-right: 10px;
          }
        }
        .card_rating{
          display: flex;
          justify-content: center;
          align-items: center;
          .rater_object{
            margin-right: 20px;
            display: flex;
            flex-direction: column;
            align-items: center;
            padding: 5px 10px;
            border-radius: 10px;
            &:last-child{
              margin-right: 0;
            }
            .rate_name{
              font-size: 18px;
            }
          }
        }
        .card_overview{
          text-overflow: ellipsis;
          -webkit-line-clamp: 6;
          -webkit-box-orient: vertical;
          display: -webkit-box;
          text-align: justify;
          font-size: 17px;
          overflow: hidden;
          max-height: 102px;
        }
        .card_action{
          max-width: 250px;
          margin: 0 auto;
          .btn_add{
            min-width: 250px;
            font-size: 18px;
          }
        }
      }
    }
  }
}
</style>



