<template>
  <div id="app">
    <vueHeader v-if="$store.getters.isAuthenticated && !notFound" ref="header" />
    <router-view/>
  </div>
</template>

<script>
import Header from '@/components/Header';
import UserService from './services/user.service';

export default {
  name: 'App',
  data() {
    return {};
  },
  components: { vueHeader: Header },
  methods: {
    manageTitle() {
      document.title = `${this.$route.name}  MediaServer`;
    },
  },
  mounted() {
    UserService.getMe()
      .then((resp) => {
        if (resp === 'auth' || resp === 'refresh') {
          if (location.pathname !== '/sign-in') {
            location.href = '/sign-in';
          }
        } else if (resp !== 'granted') {
          console.log(resp);
        }
      });
    this.manageTitle();
  },
  computed: {
    notFound() {
      return this.$route.meta.type === 'undefined';
    },
  },
};
</script>

<style>
</style>



