<template>
  <article class="main_content page_wrapper">
    <div class="loading" v-if="loading">
      <svg width="32px" height="24px">
        <polyline id="back" points="1 6 4 6 6 11 10 1 12 6 15 6"></polyline>
        <polyline id="front" points="1 6 4 6 6 11 10 1 12 6 15 6"></polyline>
      </svg>
    </div>
    <div class="page_content" v-else>
      <div class="elements_wrapper">
        <div class="media" v-for="element in media" :key="element.uid">
          <a :href="`/${element.uid}${!element.seen && element.continue > 0
              ? `?c=${element.continue}`
              : ''}`"
            class="base_link">
            <div class="media_image">
              <img :src="`/i/${element.poster}`" v-if="element.poster">
            </div>
            <div class="media_name">{{element.name}} ({{element.original_name}})</div>
          </a>
        </div>
      </div>
    </div>
  </article>
</template>
<script>
import Exchange from '../services/exchange.service';

export default {
  name: 'MediaElements',
  data() {
    return {
      backend: new Exchange(),
      mediaType: null,
      media: null,
      loading: true,
    };
  },
  methods: {
    retrieveMedia() {
      this.backend
        .get(`/api/${this.mediaType}/`)
        .then((resp) => {
          this.media = resp.body;
        })
        .catch(err => console.error(err))
        .finally(() => {
          this.loading = false;
          console.log(this.media);
        });
    },
  },
  created() {
    this.mediaType = this.$route.meta.mediaType;
    this.retrieveMedia();
  },
};
</script>
<style lang="scss" scoped>
@import "@/assets/variables.scss";

.main_content{
  .page_content{
    .elements_wrapper{
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
      justify-items: center;
      align-items: start;
      grid-gap: 20px;
      margin-top: 10px;
      .media{
        .base_link{
          font-family: $text-font;
          color: $white;
          font-size: 16px;
          text-align: center;
        }
        &_image{
          max-width: 200px;
          img{
            aspect-ratio: 2 / 3;
            width: 100%;
            border-radius: 5px;
            margin-bottom: 5px;
            pointer-events: none;
          }
        }
        &_name{
          max-width: 190px;
          margin: 0 auto;
        }
      }
    }
  }
}
</style>



