import axios from 'axios';
import authHeader from './auth-header';

function getCSRFToken() {
  const cookieValue = document.cookie
    .split('; ')
    .find(cookie => cookie.startsWith('csrftoken='));
  if (cookieValue) {
    return cookieValue.split('=')[1];
  }
  return null;
}

const csftToken = getCSRFToken();


// eslint-disable-next-line
axios.defaults.headers.common['HTTP_X_CSRFTOKEN'] = csftToken;


class AuthService {
  async login(user) {
    this.smth = null;
    return axios
      .post('/auth/', user)
      .then((resp) => {
        if (resp.data.status === 'success') {
          localStorage.setItem('user', JSON.stringify(resp.data.body));
          console.log(resp);
        }
        return resp.data.body;
      })
      .catch(err => console.log(err));
  }

  async logout() {
    this.smth = null;
    return axios
      .get('/auth/logout/', { headers: authHeader('main') })
      .then(resp => resp.data.message)
      .catch(err => console.error(err))
      .finally(() => {
        localStorage.removeItem('user');
        localStorage.removeItem('queue');
        localStorage.removeItem('aDownloads');
        location.href = '/';
      });
  }

  async register(user) {
    this.smth = null;
    return axios
      .post('/auth/register/', user)
      .then(resp => resp.data.message)
      .catch(err => console.error(err));
  }
}

export default new AuthService();



