<template>
  <header class="header">
    <div class="header_navbar">
      <div class="header_navbar-item">
        <div class="logo_wrapper">
          <a href="/" class="base_link">
            <img src="/static/logo.png" alt="logo">
          </a>
        </div>
      </div>
      <div class="header_navbar-item">
        <a href="/tv" class="base_link"></a>
      </div>
      <div class="header_navbar-item">
        <a href="/movie" class="base_link"></a>
      </div>
      <div class="header_navbar-item">
        <a href="/video" class="base_link"></a>
      </div>
      <div class="header_navbar-item last">
        <div class="search_wrapper">
          <input type="text"
            class="input_wide solo_input"
            placeholder=""
            v-model="keyword"
            @input="keywordChange"
            @focus="retrieveResults">
          <transition name="toggle">
          <div class="search_result" v-show="showSearchResult"
          :style="filteredListStyle">
            <div class="search_result_item"
              v-for="result in filteredList"
              :key="result.uid"
              @click="redirect(result.uid)">
              <div class="item_wrapper">
                <div class="item_poster">
                  <img :src="`/i/${result.poster}`" class="small_poster">
                </div>
                <div class="item_info">
                  <div class="info_name">{{result.name}} ({{result.year}})</div>
                  <div class="info_or_name">
                    <i class="ms-icon wh-20"
                      :class="result.media_type"></i>
                    {{result.original_name}}
                  </div>
                  <div class="info_rate">
                    <div class="rate_name">iMDB</div>
                    {{result.imdb_rate}}
                  </div>
                </div>
              </div>
            </div>
            <div class="empty_result" v-show="lazyLoad"
              :style="!emptyResult ? 'margin: 10px 0 !important;' : ''">
              <div class="empty_text" v-if="emptyResult">  </div>
              <a :href="keyword ? `/search?k=${keyword}` : '/search'"
                class="base_link hidden_link"> </a>
            </div>
          </div>
          </transition>
        </div>
        <div class="user_item">
          <a class="base_link" href="/user">
            <i class="ms-icon user-circle wh-30"></i>
          </a>
          <div class="queue_counter"
            v-show="showQueue">{{queueLength}}</div>
        </div>
      </div>
    </div>
  </header>
</template>
<script>
import Exchange from '../services/exchange.service';

export default {
  name: 'Header',
  data() {
    return {
      backend: new Exchange(),
      showSearchResult: false,
      searchResultLoading: false,
      keyword: null,
      results: [],
      staticUrl: '',
      showQueue: null,
      queueLength: null,
    };
  },
  methods: {
    keywordChange() {
      if (event.target.value !== '') {
        this.showSearchResult = true;
      } else {
        this.showSearchResult = false;
      }
    },
    retrieveResults() {
      if (this.results.length <= 0) {
        this.backend
          .get('/api/all/')
          .then((resp) => {
            if (resp) {
              this.results = resp.body;
            } else {
              console.log(this.backend.message);
            }
          })
          .catch(err => console.error(err));
      }
    },
    redirect(url) {
      location.href = url;
    },
    updateQueue(event) {
      if (event) {
        if (event.type === 'queueUpdate') {
          const storage = JSON.parse(event.detail.storage);
          this.queueLength = storage ? storage.length : null;
          if (this.queueLength) {
            this.showQueue = true;
          } else {
            this.showQueue = false;
          }
        }
      } else {
        const queue = JSON.parse(localStorage.getItem('queue'));
        if (queue && queue.length) {
          this.showQueue = true;
          this.queueLength = queue.length;
        }
      }
    },
    logout() {
      this.$store.dispatch('logout');
    },
  },
  mounted() {
    this.updateQueue();
    window.addEventListener('queueUpdate', this.updateQueue);
  },
  computed: {
    filteredList() {
      return this.results.filter((result) => {
        const computedObj = { ...result };
        return this.keyword
          ? Object.keys(computedObj).some(key => (`${computedObj[key]}`).toLowerCase().includes(this.keyword.toLowerCase()))
          : null;
      });
    },
    filteredListStyle() {
      let height = 100;
      if (this.filteredList.length > 1 && this.filteredList.length < 5) {
        height = 120 * this.filteredList.length;
      } else if (this.filteredList.length >= 5) {
        height = 480;
      } else {
        height = 120;
      }
      return `height: ${height}px`;
    },
    lazyLoad() {
      return !(this.filteredList.length > 1);
    },
    emptyResult() {
      return this.filteredList.length < 1;
    },
  },
  beforeDestroy() {
    window.removeEventListener('queueUpdate', this.updateQueue);
  },
};
</script>
<style lang="scss" scoped>
@import '@/assets/variables.scss';
.header{
  max-width: 1024px;
  margin: 0 auto;
  height: 70px;
  width: 100%;
  display: flex;
  align-items: center;
  &_navbar{
    display: flex;
    align-items: center;
    width: 100%;
    padding: 0 10px;
    font-family: $text-font;
    color: $white;
    font-size: 18px;
    text-shadow: $text-shadow;
    &-item{
      margin-right: 20px;
      &:last-child{
        margin-right: 0;
      }
      &.last{
        margin-left: auto;
        display: flex;
        align-items: center;
        .user_item{
          display: flex;
          position: relative;
          .queue_counter{
            position: absolute;
            right: -5px;
            top: -3px;
            font-size: 12px;
            background: $notred;
            padding: 2.5px 5px;
            border-radius: 50%;
          }
        }
        .search_wrapper{
          width: 250px;
          margin-right: 10px;
          position: relative;
          z-index: 1;
          .search_result{
            top: 35px;
            position: absolute;
            width: 100%;
            height: 100px;
            border-radius: 5px;
            display: flex;
            flex-direction: column;
            align-items: center;
            background: $dark;
            overflow: auto;
            box-shadow: $block-shadow;
            &::-webkit-scrollbar{
              -webkit-appearance: none;
              width: 0;
            }
            &_item{
              width: 100%;
              padding: 10px;
              transition: background .4s ease-out;
              cursor: pointer;
              &:hover{
                background: rgba(29, 29, 29, 0.50);
              }
              .item_wrapper{
                width: 100%;
                max-height: 100px;
                display: flex;
                .item_poster{
                  margin-right: 20px;
                  .small_poster{
                    height: 100px;
                    aspect-ratio: 2 / 3;
                    object-fit: contain;
                    border-radius: 5px;
                  }
                }
                .item_info{
                  display: flex;
                  flex-direction: column;
                  justify-content: space-between;
                  padding: 10px 0;
                  .info_name{
                    font-size: 17px;
                    font-family: $title-font;
                    text-overflow: ellipsis;
                    -webkit-line-clamp: 4;
                    -webkit-box-orient: vertical;
                    display: -webkit-box;
                    overflow-y: hidden;
                  }
                  .info_or_name{
                    display: flex;
                    align-items: center;
                    font-size: 16px;
                    .ms-icon{
                      font-size: 16px;
                      margin-right: 5px;
                    }
                  }
                  .info_rate{
                    display: flex;
                    font-size: 14px;
                    .rate_name{
                      margin-right: 5px;
                    }
                  }
                }
              }
            }
            .empty_text{
              margin-bottom: 20px;
            }
            .empty_result{
              text-align: center;
              margin: auto 0;
            }
          }
        }
        .ms-icon{
          font-size: 20px;
        }
      }
      .solo_input{
        margin-bottom: 0;
        background: $light-white;
        border: 1px solid $light-black;
        color: $white;
        backdrop-filter: blur(10px);
      }
    }
  }
}
.toggle-enter-to, .toggle-leave{
  max-height: 200px;
  opacity: 1;
}
.toggle-enter-active, .toggle-leave-active {
  transition: all .35s ease-in-out;
}
.toggle-enter, .toggle-leave-to {
  max-height: 0;
  opacity: 0;
}
</style>



