<template>
  <article class="edit_meta page_wrapper">
    <div class="loading" v-if="loading">
      <svg width="32px" height="24px">
        <polyline id="back" points="1 6 4 6 6 11 10 1 12 6 15 6"></polyline>
        <polyline id="front" points="1 6 4 6 6 11 10 1 12 6 15 6"></polyline>
      </svg>
    </div>
    <div class="page_content" v-else>
      <div class="page_bg">
        <img :src="`/i/${media.backdrop}`" class="base_image">
      </div>
      <div class="edit_meta-logo">
        <img :src="`/i/${media.logo}`" class="base_image">
      </div>
      <div class="edit_meta-container">
        <div class="leftside">
          <div class="preview_image">
            <file image="preview" preview name="preview" draggable class="preview_image"
              :existing="`/i/${media.episode.preview}`" v-model="episodePreview"/>
          </div>
          <div class="episode_name">
            <span class="episode_title">{{ media.episode.name }}</span><br>
            {{ media.episode.season }}  {{ media.episode.episode }} 
          </div>
        </div>
        <div class="rightside">
          <div class="episode_meta">
            <form @submit.prevent="handleEditMeta">
              <input type="text" required class="input_wide" placeholder=""
                v-model="media.episode.name">
              <textarea class="input_wide textarea_wide" v-model="media.episode.overview">
              </textarea>
              <input type="text" class="input_wide" placeholder=" "
                v-model="episodeMeta.videoSource">
              <div class="voices" v-if="voiceLoading || voices || episodeMeta.videoLang">
                <div class="voice_object input_wide" v-if="voiceLoading">
                   ..
                </div>
                <selecter :options="voices" v-show="voices" multiple ref="voiceSelection"
                  placeholder=" " updatable v-model="episodeMeta.videoLang" />
              </div>
              <div class="voices" v-if="!voices && !voiceLoading">
                <selecter :options="defaultLangs" multiple placeholder=" "
                  v-model="episodeMeta.voiceLang" ref="voiceSelection"/>
              </div>
              <transition name="toggle">
                <div class="subtitles_wrapper" v-show="episodeMeta.videoLang === '20'">
                  <input type="text" class="input_wide" placeholder=" "
                    v-model="episodeMeta.sub">
                  <selecter :options="langs" multiple placeholder=" "
                    v-model="episodeMeta.subLang" :defaultValue="episodeMeta.subLang"
                    updatable ref="subtitlesSelection" />
                </div>
              </transition>
              <button type="submit" class="btn btn_submit"
                ref="editMetaButton"></button>
            </form>
            <div class="auto_meta" v-if="voices">
              <button type="button" class="btn btn_submit autometa"
                @click="parseMeta" ref="autoMetaButton"> </button>
              <div class="auto_meta-caption">     </div>
            </div>
            <div class="delete_episode">
              <button type="button" class="btn btn_submit red"> </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </article>
</template>
<script>
import selecter from '@/components/Selecter';
import fileUpload from '@/components/FileUploader';
import Exchange from '../services/exchange.service';

export default {
  name: 'EditMeta',
  components: { selecter, file: fileUpload },
  data() {
    return {
      backend: new Exchange(),
      uid: null,
      loading: true,
      voiceLoading: true,
      voices: null,
      media: null,
      langs: null,
      selectedVoice: null,
      defaultLangs: null,
      episodePreview: null,
      episodeMeta: {
        videoSource: null,
        videoLang: null,
        sub: null,
        subLang: null,
      },
    };
  },
  methods: {
    retrieveMedia() {
      this.backend
        .get(`/api/episode/${this.uid}`)
        .then((resp) => {
          if (resp) {
            this.loading = false;
            this.media = resp.body;
            this.langs = resp.extra.langs;
            this.defaultLangs = resp.extra.default_langs;
          }
        })
        .catch(err => console.error(err))
        .finally(() => {
          this.retrieveVoices();
          this.applyOld();
        });
    },
    applyOld() {
      if (this.media.episode.meta) {
        this.episodeMeta.videoSource = this.media.episode.meta.video_source;
        this.episodeMeta.videoLang = this.media.episode.meta.video_lang;
        this.episodeMeta.sub = this.media.episode.meta.sub;
        this.episodeMeta.subLang = this.media.episode.meta.sub_lang;
      }
    },
    retrieveVoices() {
      const userInfo = JSON.parse(localStorage.getItem('user'));
      this.backend
        .get(`/api/voices/${this.media.media_type}`, this.media.episode.short)
        .then((resp) => {
          if (resp) {
            this.voices = resp.body.results;
          }
        })
        .catch(err => console.error(err))
        .finally(() => {
          this.voiceLoading = false;
          if (userInfo.chosen_voice) {
            this.$refs.voiceSelection.autoSelect(userInfo.chosen_voice);
          }
        });
    },
    handleEditMeta() {
      let saved = false;
      const formData = new FormData();
      Object.keys(this.episodeMeta).forEach(key => formData.append(key, this.episodeMeta[key]));
      formData.append('preview', this.episodePreview);
      this.episodeMeta.episodeUid = this.media.episode.uid;
      const prevText = this.$refs.editMetaButton.textContent;
      this.$refs.editMetaButton.textContent = '';
      this.backend
        .post(`/api/meta/edit/${this.media.media_type}/${this.media.episode.uid}`, formData)
        .then((resp) => {
          if (resp) {
            saved = true;
          }
        })
        .catch(err => console.error(err))
        .finally(() => {
          this.$refs.editMetaButton.textContent = prevText;
          if (saved) {
            setTimeout(() => {
              location.href = `/${this.media.uid}?s=${this.media.episode.season}`;
            }, 1500);
          }
        });
    },
    parseMeta() {
      const imdbID = this.media.episode.short.imdb_id;
      const prevText = this.$refs.autoMetaButton.textContent;
      this.$refs.autoMetaButton.textContent = '';
      const params = {
        e: this.media.media_type === 'tv' ? this.media.episode.episode : null,
        s: this.media.media_type === 'tv' ? this.media.episode.season : null,
        voice: this.episodeMeta.videoLang,
        sub: this.episodeMeta.subLang,
      };
      this.backend
        .get(`/api/meta/${this.media.media_type}/${imdbID}`, params)
        .then((resp) => {
          if (resp) {
            this.episodeMeta.videoSource = resp.results.video_src;
            this.episodeMeta.sub = resp.results.sub_src ? resp.results.sub_src : '';
          }
        })
        .catch(err => console.error(err))
        .finally(() => {
          this.$refs.autoMetaButton.textContent = prevText;
        });
    },
    addPreview() {},
    deletePreview() {
      this.media.episode.preview = null;
    },
  },
  created() {
    this.uid = this.$route.params.uid;
    this.retrieveMedia();
  },
};
</script>
<style lang="scss" scoped>
@import "@/assets/variables.scss";

.edit_meta{
  position: relative;
  .page_bg{
    position: absolute;
    width: 100%;
    z-index: -1;
    &:after{
      position: absolute;
      content: '';
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: radial-gradient(50% 50% at 50% 50%, rgba(29, 29, 29, 0.58) 0%, #1D1D1D 100%);
    }
  }
  &-logo{
    height: 120px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px 0;
  }
  &-container{
    display: flex;
    align-items: center;
    justify-content: space-around;
    .leftside{
      max-width: 300px;
      .preview_image{
        position: relative;
        margin-bottom: 5px;
        &:hover .preview_image-actions{
          visibility: visible;
          opacity: 1;
        }
        &-img{
          border-radius: 10px;
        }
        &-actions{
          position: absolute;
          right: 10px;
          top: 5px;
          visibility: hidden;
          opacity: 0;
          transition: all .3s ease;
          .delete_preview{
            color: $white;
            background: $blue;
            border-radius: 50%;
            padding: 5px;
            cursor: pointer;
          }
        }
      }
      .episode_name{
        font-family: $text-font;
        color: $white;
        font-size: 18px;
        max-width: 290px;
        margin: 0 auto;
        text-align: center;
      }
    }
    .rightside{
      max-width: 35%;
      .episode_meta{
        .voices{
          font-family: $text-font;
          .selection{
            height: 30px;
          }
          .voice_object{
            line-height: 30px;
            color: $dark-grey;
          }
        }
        .subtitles_wrapper{
          height: 80px;
          .selection{
            height: 30px;
            font-size: 16px;
            font-family: $text-font;
          }
        }
        .auto_meta{
          margin-top: 10px;
          font-family: $text-font;
          .autometa{
            background: $dark;
            &:hover + .auto_meta-caption{
              color: $white;
            }
          }
          &-caption{
            margin: 5px 0 0 5px;
            color: rgba(217, 217, 217, .7);
            text-shadow: $text-shadow;
          }
        }
        .delete_episode{
          margin-top: 10px;
          .red{
            background: $red;
          }
        }
      }
    }
  }
}
.toggle-enter-to, .toggle-leave{
  max-height: 70px;
  opacity: 1;
}
.toggle-enter-active, .toggle-leave-active {
  transition: all .45s ease-in-out;
}
.toggle-enter, .toggle-leave-to {
  max-height: 0;
  opacity: 0;
}
</style>



