<template>
  <div class="selection">
    <div class="selection_options" v-show="selectionsOpened" v-if="multiple">
      <div class="selection_options-option"
        v-for="(value, name, index) in options" :key="index"
        @click="selectOption(value, name)">
        {{ value }}
      </div>
    </div>
    <div class="selection_options" v-show="selectionsOpened" v-else>
      <div class="selection_options-option" v-for="option in options" :key="option"
        @click="selectThis(option)">
        {{ option }}
      </div>
    </div>
    <div class="selection_selected option_selected" v-if="selected" @click="clearSelection">
      {{ selected }}
    </div>
    <div class="selection_selected" v-else @click="toggleSelections" ref="defaultSelection">
      <div class="selection_selected-default">{{ placeholder }}</div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'selecter',
  props: {
    options: {},
    multiple: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: ' ',
    },
    updatable: {
      type: Boolean,
      default: false,
    },
    defaultValue: {
      type: String,
      default: null,
    },
  },
  data() {
    return {
      selectionsOpened: false,
      selected: null,
    };
  },
  methods: {
    selectThis(value) {
      this.selected = value;
      this.selectionsOpened = false;
      this.$emit('input', value);
    },
    selectOption(value, name) {
      this.selected = value;
      this.selectionsOpened = false;
      this.$emit('input', name);
    },
    autoSelect(value) {
      if (this.options[value]) {
        this.selected = this.options[value];
        this.$emit('input', value);
      }
    },
    toggleSelections() {
      if (this.selectionOpened) {
        this.$refs.defaultSelection.classList.remove('opened');
      } else {
        this.$refs.defaultSelection.classList.add('opened');
      }
      this.selectionsOpened = !this.selectionOpened;
    },
    clearSelection() {
      this.selected = null;
      this.$emit('input', null);
    },
    keyboardHandler() {
      if (this.selectionsOpened) {
        if (event.which === 27) {
          this.selectionsOpened = false;
          this.$refs.defaultSelection.classList.remove('opened');
        }
      }
    },
  },
  mounted() {
    window.addEventListener('keyup', this.keyboardHandler);
    if (this.options && this.$attrs.value) {
      this.selected = this.options[this.$attrs.value];
    }
  },
  beforeDestroy() {
    window.removeEventListener('keyup', this.keyboardHandler);
  },
  updated() {
    this.$nextTick(() => {
      if (this.updatable && !this.selected) {
        if (this.options) {
          this.selected = this.options[this.$attrs.value];
        }
      }
    });
  },
};
</script>
<style lang="scss" scoped>
@import "@/assets/variables.scss";
.selection{
  position: relative;
  margin-bottom: 10px;
  &_options{
    position: absolute;
    z-index: 2;
    background: $white;
    font-family: $text-font;
    top: calc(100% + 5px);
    max-height: 150px;
    overflow: auto;
    border-radius: 5px;
    width: 100%;
    &::-webkit-scrollbar{
      -webkit-appearance: none;
      width: 4px;
      background: rgba(217, 217, 217, .7);
      border-radius: 5px;
    }
    &::-webkit-scrollbar-thumb{
      width: 2px;
      -webkit-appearance: none;
      background: $blue;
      border-radius: 3px;
    }
    &-option{
      font-size: inherit;
      width: 100%;
      color: $black;
      padding: 5px 15px;
      &:hover{
        background: $blue;
        color: $white;
        cursor: pointer;
      }
    }
  }
  &_selected{
    color: $dark;
    font-size: 16px;
    background: $white;
    border-radius: 5px;
    padding: 5px 10px;
    height: inherit;
    font-family: $text-font;
    cursor: pointer;
    color: $dark-grey;
    display: flex;
    align-items: center;
    &.opened{
      &:after{
        transform: rotateX(180deg);
      }
    }
    &:after{
      transition: all .3s ease;
      position: absolute;
      right: 10px;
      top: calc(50% - 7px);
      font-family: 'FontAwesome';
      font-size: 14px;
      content: '\f078';
      opacity: .7;
    }
  }
  &_selected.option_selected{
    position: relative;
    color: $black;
    &:after{
      position: absolute;
      right: 10px;
      top: calc(50% - 7px);
      font-family: 'FontAwesome';
      font-size: 14px;
      content: '\f057';
      opacity: .7;
    }
  }
}
</style>



